.blog_page_title {
  display: flex;
  flex-direction: column;
  align-items: start;
  max-width: 324px;
  width: 100%;
  h1 {
    margin-bottom: .4em;
  }
}
.blog_list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: start;
  width: 100%;
  margin-bottom: 4em;
  grid-gap: 3em;
}
.blog_list > a {
  min-height: 69.7em;
  height: 100%;
}
.blog_list_item {
  position: relative;
  
  .content_main-img {
    width: 100%;
    // max-height: 285px;
    height: 28.5em;
    display: block;
    overflow: hidden;
    margin-bottom: 3.2em;
    position: relative;
    
    > span {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: auto;
      
      > img {
        width: 100%;
        height: auto;
      }
    }
  }
  
  .content_text {
    
  }
  
  
  .article_card_footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 2em;
    width: 100%;
    
    .podcast_player {
      @media all and (max-width: 700px) {
        padding-top: 0;
        
        .controls {
          justify-content: flex-end;
        }
      }
    }
  }
  
  
  // .blog_count {
  //   @media all and (max-width: 700px) {
  //     margin-left: 0;
  //   }
  // }
}
.blog_list_item_content {
  grid-template-rows: max-content max-content 1fr;
  display: grid;
  padding: 2em 3.2em 2em 2.9em;
  height: 100%;
  //display: flex;
  //flex-direction: column;
  border: 2px solid rgba(149, 136, 232, 1);
  // border-top: none;
  // border-right: none;
  background: #f5f8ff;
  z-index: 2;
  position: relative;
  
  @media all and (max-width: 480px) {
    border: 1px solid rgba(149, 136, 232, 1);
  }
  
  &:before {
    content: '';
    display: inline-block;
    left: -1px;
    top: -2px;
    width: 42.5em;
    height: 39.55em;
    position: absolute;
    background-size: 42.5em;
    background-repeat: no-repeat;
    background-position: top left;
    background-image: url('../img/blog_card_frame.svg');
    z-index: 2;
    
    @media all and (max-width: 1380px) {
      width: 38em;
      background-position: 100% 0;
    }
    @media all and (max-width: 1100px) {
      width: 51.6em;
      height: 48em;
      background-position: 94% 0;
    }
    @media all and (max-width: 980px) {
      width: 36.2em;
      background-position: 110% 0;
    }
    @media all and (max-width: 700px) {
      width: 52.2em;
      background-position: 80.3% 0;
    }
    @media all and (max-width: 480px) {
      background-size: 38.7em;
      width: 52.2em;
      background-position: 9.2em 0;
      top: -1px;
    }
  }
  &:after {
    content: '';
    display: inline-block;
    width: 2em;
    height: 2em;
    position: absolute;
    top: -3px;
    left: ~'calc(100% - 3px)';
    background: #f5f8ff;
    z-index: 1;
  }
  .content_text {
    &-title {
      line-height: 1.3;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      overflow: hidden;
    }
  }
}
.blog_list_item_decor {
  top: 1px;
  left: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  width: 15px;
  height: 100%;
  position: absolute;
  background: #f5f8ff;
  // border-left: 2px solid #9588e8;
}
.blog_list_item_decor > img {
  width: 100%;
  position: relative;
  height: 229px;
  top: -2px;
  right: 3px;
  z-index: 2;
}
.blog_list_item_decor_fon {
  width: 100%;
  height: 165px;
  background: #f5f8ff;
  // position: relative;
  position: absolute;
  // z-index: 3;
  left: 0;
  top: 0;
  width: 42.5em;
  height: 39.55em;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: top left;
  background-image: url("data:image/svg+xml;base64,");
  // right: 3px;
  // border-right: 2px solid rgba(149, 136, 232, 1);
}

.blog_list_item_decor_fon:before, .blog_list_item_decor_fon:after {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  background: #f5f8ff;
  border-left: 2px solid rgba(149, 136, 232, 1);
  border-right: 2px solid #f5f8ff;
  display: none;
}
.blog_list_item_decor_fon:before {
  transform: rotate(312deg);
  top: -9px;
  right: -3px;
  height: 25px
}
.blog_list_item_decor_fon:after {
  border-right: 0;
  transform: rotate(219deg);
  bottom: -10px;
  right: 2px;
  height: 15px;
  z-index: -1;
}
