.blog_item-divider {
  margin-top: 4em;
  width: 100%;
  height: 10px;
  position: relative;
  display: flex;
  align-items: center;
}
.blog_item-divider > .divider_line {
  width: 100%;
  height: 1px;
  border-bottom: 1px solid #252C32;
  position: relative;
}
.divider_left, .divider_right {
  width: 4em;
  height: 1em;
  position: absolute;
  background: #252C32;
  clip-path: polygon(0 0, 68.57% 0, 100% 50%, 68.57% 100%, 0 100%);
  top: .01rem;
}
.divider_left {
  right: 0;
  transform: rotate(180deg);
}
.divider_right {
  transform: rotate(361deg);
  left: 0;
}
.blog_subcribe_form {
  margin-bottom: 6.4em;
  background: rgba(119, 106, 202, 1);
  min-height: 26.4em;
  padding: 3em 4.5em;
  position: relative;
  // padding: 120px 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  margin-left: -4.7em;
  margin-right: -4.7em;
  
  @media all and (max-width: 980px) {
    padding-left: 0;
    padding-right: 0;
  }
  
  .bg_pos {
    left: 50%;
    top: 189%;
    transform: translate(-50% , -50%);
    position: absolute;
  }
  .bgr1, .bgr2 {
    width: 110em;
    height: 110em;
  }
  .bgr1 {
    background: url(../img/circles_in_in.png) no-repeat center;
    animation: ratate_industry_demo_revert 50s linear infinite;
    background-size: contain;
  }
  .bgr2 {
    background: url(../img/circles_out_in.png) no-repeat center;
    animation:ratate_industry_demo 50s linear infinite;
    background-size: contain;
  }
}
.blog_subcribe_form:before {
  content: '';
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  border: 3.5em solid transparent;
  border-left: 3.5em solid #f5f8ff;
  border-top: 3.5em solid #f5f8ff;
}
.blog_subcribe_form:after {
  content: '';
  display: inline-block;
  width: 25em;
  height: 1.3em;
  position: absolute;
  bottom: 2em;
  right: -7.6em;
  transform: rotate(-47.9deg);
  background: #f5f8ff;
}
.blog_subcribe_form_bgi {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: url("../img/subscribe_round.svg") no-repeat center center;
  background-size: cover;
  mix-blend-mode: color-burn;
  z-index: 1;
}
.blog_subcribe_form_wrapper {
  width: 100%;
