//     : @import './src/less/blocks/btn.less';

.btn {
    padding: 1.1em 3em;
    border: 1px solid @dark;
    position: relative;
    font-weight: 700;
    color: @dark;
    text-transform: uppercase;

    &--white {
        background: #f5f5f5;
        border-color: #f5f5f5;

        span {
            color: @dark;
        }

        &:after, &:before {
            background: #f5f5f5 !important;
            border-color: #f5f5f5 !important;
        }
    }

    &--violet {
        background: @violet;
        border-color: @violet;

        span {
            color: @white;
        }

        &:after, &:before {
            background: @violet !important;
            border-color: @violet !important;
        }
    }
    
    &--left {
        margin-left: 5.3em;

        &:before, 
        &:after {
            content: '';
            display: inline-block;
            height: 100%;
            border: 1px solid #252C32;
            position: absolute;
            top: -1px;
            transition: .3s;
        }
        &:before {
            left: -5.4em;
            width: 2.4em;
        }
        &:after {
            width: 1em;
            left: -2em;
        }

        &:hover {
            border-left-color: transparent;
            &:after {
                width: 2em;
                border-right: none;
            }
        }
    }
    &--right {
        margin-right: 5.3em;

        &:before, 
        &:after {
            content: '';
            display: inline-block;
            height: 100%;
            border: 1px solid #252C32;
            position: absolute;
            top: -1px;
            transition: .3s;
        }
        &:before {
            right: -5.4em;
            width: 2.4em;
        }
        &:after {
            width: 1em;
            right: -2em;
        }

        &:hover {
            border-right-color: transparent;
            &:after {
                width: 2em;
                border-left: none;
            }
        }
    }

    &--icon_discord {
        padding: 1.2em 1.5em 1.2em 4.5em;

        background-image: url('../img/icon_discord.svg');
        background-position: 1em 50%;
        background-size: 2.2em;
        background-repeat: no-repeat;
    }
}
