.pricing {
  margin-bottom: 6.4em;
  .text_page_title {
    max-width: 10em;
  }
  &_send {
    width: 41em;
    float: right;
    height: auto;
    margin-top: 3.2em;
    position: relative;
    &:after, &:before {
      content: '';
      display: inline-block;
      width: 6px;
      height: 105%;
      top: -2.5%;
      background: #f5f8ff;
      position: absolute;
      z-index: 10;
    }
    &:before {
      right: 4em;
    }
    &:after {
      right: 2.4em;
    }
    input {
      cursor: pointer;
      font-size: 16px !important;
      font-weight: 700;
      border-radius: 0;
      padding: .8em 5.2em .8em 2.2em;
      font-family: 'Inter Tight', sans-serif;
      border: 1px solid #776ACA;
      display: inline-block;
      transition: all .3s;
      line-height: 1 !important;
      color: #fff;
      text-align: center;
      width: 100%;
      text-transform: uppercase;
      position: relative;
      background: #776ACA;
    }
  }
  &_content {
    display: grid;
    grid-template-columns: 42.3em 1fr;
    align-items: center;
    grid-gap: 3em;
    &_img {
      max-width: 423px;
      width: 100%;
      will-change: transform;
    }
    .form_item {
      max-width: 85em;
      margin-left: auto;
      width: 100%;
    }
    &_block {
      display: flex;
      align-items: end;
      justify-content: space-between;
      flex-direction: column;
      height: 100%;
      &_item {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        &_title {
          width: 100%;
          font-weight: 400;
          font-size: 16px;
          color: #252C32;
          margin-right: 1.9em;
        }
        &_input {
          width: 100%;
          position: relative;
          span {
            position: absolute;
            font-family: 'Inter Tight', sans-serif;
            font-weight: 400;
            font-size: 16px;
            color: #252C32;
            top: 50%;
            transform: translateY(-55%);
            left: 1em;
          }
          input {
            border: none;
            outline: none;
            font-family: 'Inter Tight', sans-serif;
            font-size: 1.6em;
            color:rgba(37, 44, 50, 1);
            line-height: 2.8em;
            padding: .8em .8em .8em 1.9em;
            border-bottom: 1px solid rgba(37, 44, 50, 1);
            width: 100%;
            height: 45px;
            background: #f5f8ff;
            transition: .2s linear;
            &::placeholder {
              font-family: 'Inter Tight', sans-serif;
              color: rgba(110, 124, 135, 1);
            }
            &:hover, &:focus {
              border-bottom: 1px solid rgba(149, 136, 232, 1)
            }
          }
        }
      }
    }
  }
}

@media all and (max-width: 1380px) {
  .pricing_send {
    width: 32.6em;
  }

}
@media all and (max-width: 1100px) {
  .pricing_send {
    width: 26em;
  }
}
@media all and (max-width: 980px) {
  .pricing_content {
    grid-template-columns: auto;
    place-items: center;
  }
  .pricing_content_block_item {
    align-items: baseline;
    margin-bottom: 3.4em;
  }
  .pricing_send {
    width: 100%;
    max-width: none;
    float: none;
  }
}
@media all and (max-width: 700px) {
  .pricing {
    margin-bottom: 3.2em;
  }
  .pricing_content_block_item {
    margin-bottom: 3.6em;
  }
  .pricing_content_block_item_input span {
    left: .5em;
  }
  .pricing_content_block_item {
    display: block;
  }
  .pricing_content_img {
    margin-bottom: 0em;
    max-width: 31em;
  }
  .pricing_send {
    margin-top: 0;
  }
  .page_title_block--pricing .pricing_description {
    margin-left: 0;
  }
}
