//     : @import '../_source_dev/less/blocks/about_list.less';


.about_list, .schemes_rsa {
    padding: 6.5em 0;

    @media all and (max-width: 980px) {
        padding-top: 5.5em;
    }

    .inner {
        background: @violet;
        color: #fff;
        padding: 12em 17.3em 12em 14.7em;
        position: relative;
        margin-left: -4.7em;
        margin-right: -4.7em;
        overflow: hidden;

        @media all and (max-width: 1100px) {
            padding: 12em 10em;
        }
        @media all and (max-width: 980px) {
            padding: 11em var(--container_padding);
            margin-left: ~'calc(var(--container_padding) * -1)';
            margin-right: ~'calc(var(--container_padding) * -1)';
        }
        .bgr {
            position: absolute;
            display: inline-block;
            width: 138.2em;
            height: 123.8em;
            position: absolute;
            background-image: url('../img/bgr_circle.webp');
            mix-blend-mode: color-burn;
            top: 50%;
            left: 50%;
            opacity: 0.7;
            transform: translate(-50%, -50%);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
            z-index: 1;
        }

        * {
            position: relative;
            z-index: 100;
        }

        &:before {
            content: '';
            display: inline-block;
            position: absolute;
            top: 0;
            left: 0;
            border: 3.5em solid transparent;
            border-left: 3.5em solid @cream;
            border-top: 3.5em solid @cream;

            @media all and (max-width: 480px) {
                border: 2.5em solid transparent;
                border-left: 2.5em solid @cream;
                border-top: 2.5em solid @cream;
            }
        }
        &:after {
            content: '';
            display: inline-block;
            width: 25em;
            height: 1.3em;
            position: absolute;
            bottom: 2em;
            right: -7.6em;
            transform: rotate(-47.9deg);
            background: @cream;
        }

        .section_title {
            margin-bottom: 0.85em;
            text-align: right;

            @media all and (max-width: 980px) {
                text-align: left;
                margin-bottom: 1.25em;
            }
        }

        .line_separator {
            background: #C9C2F933;
            margin-left: -0.7em;
            width: ~'calc(100% + 5.2em)';

            &:before,
            &:after {
                background-image: url("data:image/svg+xml;base64,");
            }

            @media all and (max-width: 480px) {
                width: 100%;
            }
        }

        .list {
            display: flex;
            flex-direction: column;
            position: relative;
            padding-left: 7.8em;

            @media all and (max-width: 980px) {
                padding-left: 0;
                padding-top: 2em;
            }

            &:before {
                content: '';
                display: inline-block;
                width: 8em;
                height: 76em;
                position: absolute;
                top: 0.5em;
                left: 0;
                background-image: url(../img/list_line.svg);
                background-size: contain;
                background-repeat: no-repeat;
                background-position: center;

                @media all and (max-width: 700px) {
                    left: auto;
                    right: 100%;
                }
                @media all and (max-width: 480px) {
                    left: auto;
                    right: ~'calc(100% - 3.6em)';
                }
            }
            &:after {
                content: '';
                display: inline-block;
                width: .06em;
                background: #C9C2F933;
                height: ~'calc(100% - 3.7em)';
                bottom: 0;
                left: 7.9em;
                position: absolute;
                opacity: 1;
                z-index: 99;

                @media all and (max-width: 700px) {
                    left: auto;
                    right: 100%;
                }

                @media all and (max-width: 480px) {
                    left: auto;
                    right: ~'calc(100% - 3.6em)';
                }
            }

            .item {
                display: grid;
                grid-template-columns: 1fr 1.55fr;
                grid-gap: 3.6em;
                padding: 3.5em 0 2.7em 3.9em;
                border-bottom: 1px solid #C9C2F933;

                @media all and (max-width: 980px) {
                    grid-template-columns: 1fr;
                    grid-gap: 1.4em;
                    padding: 2.2em 0 0.8em 12.3em;
                }
                @media all and (max-width: 700px) {
                    grid-gap: 2em;
                    padding: 2em;
                    padding-right: 0;
                    grid-template-columns: 1fr;
                }
                @media all and (max-width: 480px) {
                    margin-left: 3.6em;
                }
                &:last-child {
                    border: none;
                    padding-bottom: 0;
                }

                .text_m {
                    p {
                        margin-bottom: 0.25em;

                        &:last-child {
                            margin-bottom: 0;
                        }
                    }
                }
            }
        }
    }

    .color_violet {
        color: @lavender !important;
    }
}
