//     : @import '../_source_dev/less/blocks/sphere.less';


.sphere_block {
    padding: 4em 0;
    .sphere_inner {
        width: 74.5em;
        height: 74.5em;
        position: relative;
        margin-left: auto;
        margin-right: auto;

        @media all and (max-width: 980px) {
            font-size: 1em;
        }
        @media all and (max-width: 700px) {
            font-size: 0.6em;
        }
        .section_title {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 10;
            white-space: nowrap;

            @media all and (max-width: 480px) {
                font-size: 4.2em;
            }
        }
        
        #sphere_item {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 1;
        }
        
        .tag {
            border: 1px solid @violet;
            position: absolute;
            padding: 0.8em 1.2em .8em 2.8em;
            z-index: 99;
            white-space: nowrap;
            background: #fff;
            
            &:before {
                content: '';
                display: inline-block;
                width: 0.8em;
                height: 0.8em;
                position: absolute;
                top: 50%;
                left: 1.2em;
                transform: translateY(-50%);
                border-radius: 50%;
                background: #6e7c87;
            }
            
            .text_ml {
                line-height: 1.1;

                @media all and (max-width: 700px) {
                    font-size: 2.9667em;
                }
                @media all and (max-width: 480px) {
                    font-size: 2.74em;
                }
            }
        }
        .tag.active {
            &:before {
                background: #47d16c;
            }
            .text_ml {
                color: #9588e8;
                position: relative;
                
                &:after {
                    content: '';
                    display: inline-block;
                    width: 100%;
                    height: 1px;
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    background: #9588E8;
                }
            }
        }
        .tag_item_1 {
            top: 6.3em;
            left: 50.9em;
        }
        .tag_item_2 {
            top: 4.8em;
            left: 20.2em;
        }
        .tag_item_3 {
            top: 18.2em;
            left: 1.6em;
        }
        .tag_item_4 {
            top: 17.4em;
            left: 28.9em;
        }
        .tag_item_5 {
            top: 16em;
            left: 53.1em;
        }
        .tag_item_6 {
            top: 27.9em;
            left: 9.8em;
        }
        .tag_item_7 {
            top: 37.9em;
            left: -1.6em;
        }
        .tag_item_8 {
            top: 27.9em;
            left: 45.6em;

            @media all and (max-width: 980px) {
                left: 36.6em;
            }

            @media all and (max-width: 700px) {
                left: 33em;
            }
        }
        .tag_item_9 {
            top: 50.4em;
            left: 3.3em;
        }
        .tag_item_10 {
            top: 46.4em;
            left: 45.6em;
        }
        .tag_item_11 {
            top: 62.7em;
            left: 26em;
        }
        .tag_item_12 {
            top: 54.8em;
            left: 61.4em;

            @media all and (max-width: 980px) {
                left: 55.4em;
            }
        }
    }
}
