@white: #ffffff;
@cream: rgba(245, 248, 255, 1);
@black: rgba(37, 44, 50, 1);
@violet: rgba(119, 106, 202, 1);
@purple: #9588E8;
@dark: #252C32;
@lavender: #C9C2F9;
@dark_gray: #6E7C87;

:root {
    --container_width: 1380px;
    --container_padding: 45px;
}
@media all and (max-width: 1380px) {
    :root {
        --container_width: 1100px;
        --container_padding: 30px;
    }   
}
@media all and (max-width: 1100px) {
    :root {
        --container_width: 980px;
        --container_padding: 30px;
    }   
}
@media all and (max-width: 980px) {
    :root {
        --container_width: 700px;
        --container_padding: 30px;
    }   
}
@media all and (max-width: 700px) {
    :root {
        --container_width: 480px;
        --container_padding: 20px;
    }   
}
@media all and (max-width: 480px) {
    :root {
        --container_width: 100%;
        --container_padding: 2em;
    }   
}