.AboutUs {
  margin-bottom: 6.4em;
  .page_title_block {
    grid-gap: 3.223em;
  }
  &_content {
    display: grid;
    grid-template-columns: 42.5em auto;
    align-items: start;
    padding: 0 11.2em;
    gap: 12.5em;

    &_img {
      max-width: 42.5em;
      margin-top: -2.5em;

      @media all and (max-width: 700px) {
        max-width: 50vw;
        margin-left: auto;
        margin-right: auto;
      }

      img {
        width: 100%;
        will-change: transform;
      }
    }
    &_text {
      font-family: 'Inter Tight', sans-serif;
      font-style: normal;
      font-weight: 400;
      letter-spacing: 0.02em;
      .text_md();
      p {
        margin-bottom: 1.1em;
      }
      p:last-child {
        margin-bottom: 0
      }
    }
  }
}

@media all and (max-width: 1380px) {
  .AboutUs_content {
    padding: 0;
  }
}
@media all and (max-width: 980px) {
  .AboutUs_content {
    grid-template-columns: auto;
    gap: 5em;
  }
}
@media all and (max-width: 700px) {
  .AboutUs {
    margin-bottom: 4.31em;
  }
  .AboutUs_content {
    gap: 3.232em;
  }
  .AboutUs_content_img {
    margin-top: -1.6em;
  }
}

