        //     @media all and (max-width: 980px) { width: 21em; }
        // }
        .controls .volume-container.expanded .volume-slider {
            width: 9em;
            @media all and (max-width: 1380px) and (min-width: 701px) { width: 5em; }
        }
    }
}
.blog_detailed_info {
    .audio-player .controls .volume-container.expanded .volume-slider {
        @media all and (max-width: 980px) and (min-width: 701px) {
            width: 4em;
        }
    }
}

