.player_indicator {
    display: none;
    @media all and (max-width: 700px) {
        display: inline-block;
    }
    .toggle-play {
        .togglePlayBtn();
    }
    .toggle-play.play:after {
        .playBGI();
    }
    .toggle-play.pause:after {
        .pauseBGI();
    }
    .btn_name {
        display: inline-block;
        .btn_text_item {
            font-size: 1.705em;
            display: none;
            @media all and (max-width: 480px) {
                font-size: 1.8em;
            }
        }
    }
    .btn_name.play {
        .word_play { display: inline-block; }
    }
    .btn_name.pause {
        .word_pause { display: inline-block; }
    }
}

.togglePlayBtn {
    border: none;
    // width: 3.2em;
    height: 3.2em;
    margin-right: 0.5em;
    display: flex;
    align-items: center;
    padding-left: 4em;
    width: auto;
    position: relative;
    
    &:before {
        display: none;
    }
    
    &:after {
        content: '';
        display: inline-block;
        background: none;
        border-radius: 0;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        width: 3.2em;
        height: 3.2em;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
    }
}
.playBGI {
    background-image: url("data:image/svg+xml;base64,");
}
.pauseBGI {
    background-image: url("data:image/svg+xml;base64,");
}

.podcast_player.visible {
    .audio-player {
        @media all and (max-width: 700px) {
            visibility: visible;
            height: auto;
            opacity: 1;
            z-index: 99999;
        }
    }
}

.audio-player {
    height: 3.6em;
    width: 100%;
    max-width: 350px;
    color: #252C32;
    // overflow: hidden;
    
    @media all and (max-width: 700px) {
        visibility: hidden;
        opacity: 0;
        height: 0;
        position: fixed;
        width: 100vw;
        max-width: initial;
        padding: 1.5em 0;
        left: 50%;
        transform: translateX(-50%);
        background: @lavender;
        display: flex;
        justify-content: center;
        align-items: center;
        
        &:after {
            content: '';
            display: inline-block;
            width: 120vw;
            left: 50%;
            transform: translateX(-50%);
            top: 0;
            height: 100%;
            background: @lavender;
            z-index: -1;
            position: absolute;
        }
    }
    
    .controls {
        width: 100%;
        gap: 1em;        
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media all and (max-width: 700px) {
            max-width: var(--container_width);
            padding: 0 var(--container_padding);
        }
    }
    
    .close_btn {
        width: 2.2em;
        height: 2.2em;
        background-image: url("data:image/svg+xml;base64,");
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        cursor: pointer;
        display: none !important;
        margin-left: 0.5em;
        
        @media all and (max-width: 700px) {
            display: inline-block !important;
        }
    }
    
    
    
    .timeline {
        background: #D5D8DD;
        // width: 14em;
        width: auto;
        flex-grow: 1;
        padding: 0 1em;
        height: 4px;
        position: relative;
        cursor: pointer;
        margin: 0 auto;
        transition: .3s;
    }
    .timeline.small {
        // width: 3em;
        opacity: 0;
        visibility: hidden;
    }
    .timeline .progress {
        background: #252C32;
        width: 0%;
        height: 100%;
        transition: 0.25s;
        position: absolute;
        left: 0;
        top: 0;
        
        &:after {
            content: '';
            display: inline-block;
            width: 10px;
            height: 10px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: -10px;
            border-radius: 50%;
            background: #252C32;
        }
    }
    
    .controls > * {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .play-container {
        .btn_name {
            display: none;
            
            @media all and (max-width: 980px) {
                // display: inline-block;
            }
        }
    }
    .controls .toggle-play {
        cursor: pointer;
        position: relative;
        left: 0;
        width: 3.6em;
        height: 3.6em;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
    }
    .controls .toggle-play.play:hover {
        transform: scale(1.1);
    }
    .controls .toggle-play.play {
        .playBGI();
    }
    .controls .toggle-play.pause {
        .pauseBGI();
    }
    // .controls .toggle-play.pause:before {
    //     position: absolute;
    //     top: 0;
    //     left: 0px;
    //     background: #252C32;
    //     content: "";
    //     height: 15px;
    //     width: 3px;
    //     border-radius: 30px;
    // }
    // .controls .toggle-play.pause:after {
    //     position: absolute;
    //     top: 0;
    //     right: 10px;
    //     background: #252C32;
    //     content: "";
    //     height: 15px;
    //     width: 3px;
    //     border-radius: 30px;
    // }
    .controls .time {
        display: flex;
        font-size: 1.4em;
        margin: 0 auto;
        align-items: baseline;
        width: 6em;
        @media all and (max-width: 980px) {
            width: 4.5em;
        }
        @media all and (max-width: 700px) {
            font-size: 1.556em;
            width: 6em;
        }
        @media all and (max-width: 480px) {
            font-size: 1.8em;
        }
        
        .current {
            color: #252C32;
            scale: initial;
        }
    }
    .controls .time > * {
        padding: 0 2px;
    }
    .controls .volume-container {
        cursor: pointer;
        position: relative;
        z-index: 2;
        margin-left: auto;
        transition: width .3s;
        width: 2.5em;
    }
    .controls .volume-container .volume-button {
        width: 2em;
        height: 2em;
        display: flex;
        align-items: center;
        background-image: url("data:image/svg+xml;base64,");
        background-size: 1.8em;
        background-position: center;
        background-repeat: no-repeat;
        position: relative;
        margin-left: auto;
        
        .volume {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            
            &:before,
            &:after {
                content: '';
                display: inline-block;
                width: 0;
                height: 2px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) rotate(45deg);
                background: #252c32;
                transition: .3s;
                z-index: 2;
            }
            &:after {
                transform: translate(-40%, -50%) rotate(45deg);
                background: #fff;
                z-index: 1;
            }
        }
        .volume.icono-volumeMute {
            &:before,
            &:after {
                width: 2em;
            }
        }
    }
    .controls .volume-container .volume-slider {
        position: absolute;
        right: 2.5em;
        top: 50%;
        transform: translateY(-50%);
        // top: 15px;
        z-index: -1;
        width: 0;
        height: 4px;
        background: #D5D8DD;
        transition: 0.25s;
        margin-right: 0.5em;
    }
    .controls .volume-container .volume-slider .volume-percentage {
        background: #252C32;
        height: 100%;
        width: 75%;
        transition: .3s;
    }
    .controls .volume-container.expanded {
        width: 20em;
        
        .volume-slider {
            width: 15.5em;
        }
    }
}


.player_box.visible {
    opacity: 1;
    height: 5em;
    visibility: visible;
    display: inline-block;
    @media all and (min-width: 701px) {
        display: none;
    }
}
.player_box {
    opacity: 0;
    visibility: hidden;
    height: 0;
    display: none;
    transition: .3s;
    position: sticky !important;
    left: 0;
    width: 100%;
    z-index: 999;
    background: @lavender;
    padding: 1.2em 0;
    
    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;
        
        .podcast_player {
            width: 90%;
            
            .audio-player .timeline {
                background: #A19AD1;
            }
        }
    }
    
    
    .close_btn {
        width: 2.2em;
        height: 2.2em;
        background-image: url("data:image/svg+xml;base64,");
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        cursor: pointer;
    }
}