.federated_learning_common {
  .mb-120 {
    margin-bottom: 7.2em;
  }
  p {
    margin-bottom: 1.01em;
  }
  img {
    width: 100%;
    height: auto;
    will-change: transform;
  }
  .federated_learning_common_img_left {
    padding-right: 8em;
  }

  .federated_learning_common_separator {
    position: relative;
    &:after {
      content: '';
      display: inline-block;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: @black;
      position: relative;
      margin: 3.2em 0;
    }
  }
  .federated_learning_common_grid {
    display: grid;
    align-items: center;
    justify-content: space-between;
    gap: 3.2em;
    grid-template-columns: repeat(2, auto);
    .coll {
      max-width: 52em;
    }
    .custom_coll_div {
      p:last-child {
        margin-bottom: 0;
      }
      div {
        padding: 1.5em 2em;
        background: #776ACA;
        width: 100%;
        height: auto;
        font-weight: 400;
        color: #FFFFFF;
        position: relative;
        margin-bottom: 1.5em;
        span {
          font-weight: 600;
        }
        &:before {
          content: '';
          display: inline-block;
          position: absolute;
          top: 0;
          left: 0;
          border: .5em solid transparent;
          border-left: .5em solid #f5f8ff;
          border-top: .5em solid #f5f8ff;
        }
        &:after {
          content: '';
          display: inline-block;
          position: absolute;
          bottom: 0;
          right: 0;
          border: .5em solid transparent;
          border-right: .5em solid #f5f8ff;
          border-bottom: .5em solid #f5f8ff;
        }
      }
    }
  }
  .subtitle {
    //margin-top: 4em;
  }
  h1, h2, h3 {
    span {
      color: @purple;
    }
  }
  .federated_learning_common_left {
    margin-left: 11em;
  }
  .line_separator {
    //margin: 4em 0;
  }
}

.federated_learning {
  .federated_learning_title {
    max-width: 6em;
    width: 100%;
    line-height: 1;
  }
  .federated_learning_content {
    .federated_learning_content_obstacle {
      .federated_learning_content_obstacle_list {
        margin-top: 2.4em;
        .federated_learning_content_obstacle_list_item {
          display: flex;
          align-items: start;
          //justify-content: space-between;
          margin-bottom: .8em;
          .text_m {
            line-height: 1.5;
            padding-left: 1em;
          }
        }
      }
    }
    .federated_learning_content_federated {
      .federated_learning_common_grid {
        gap: 13.2em;
        grid-template-columns: repeat(2, 1fr);
      }
      .federated_learning_content_federated_right_col {
        strong {
          font-size: 1.23em;
        }
      }
      .federated_learning_content_federated_list {
        position: relative;
        .federated_learning_content_federated_list_item {
          display: flex;
          align-items: start;
          justify-content: space-between;
          .federated_learning_content_federated_list_item_separator {
            margin-top: 2.4em;
            font-weight: 600;
            div {
              position: absolute;
              max-width: 10em;
              left: 50%;
              transform: translateX(-50%);
            }
          }
          .federated_learning_content_federated_list_item_element {
            max-width: 19em;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            text-align: center;
            img {
              max-width: 8em;
              width: 100%;
              margin-bottom: 1.85em;
            }
          }
        }
      }
    }
  }
}

.basic_principles {
  .basic_principles_title {
    max-width: 80em;
  }
  .basic_principles_content {
    .basic_principles_content_step_list {
      .federated_learning_common_separator {
        &:last-child {
          &:after {
            content: none;
          }
        }
      }
      .basic_principles_content_step_list_item {
        .basic_principles_content_step_list_item_description {
          margin-top: 2.4em;
        }
      }
    }
    .basic_principles_content_card {
      .basic_principles_content_card_item {
        height: 100%;
        display: flex;
        flex-direction: column;
        .card_item_white {
          background-color: @white;
          color: @black;
        }
        .card_item_violet {
          color: @white;
          background-color: @violet;
          .federated_learning_common_separator {
              &:after {
                background-color: rgba(201, 194, 249, 0.2);
              }
          }
        }
        .basic_principles_content_card_item_card {
          margin-top: 4em;
          width: 100%;
          min-height: 76.7em;
          height: 100%;
          padding: 5.7em 5.5em;
          position: relative;
          p {
            margin-bottom: 0;
          }
          &:before {
            content: '';
            display: inline-block;
            position: absolute;
            top: 0;
            left: 0;
            border: 1.5em solid transparent;
            border-left: 1.5em solid #f5f8ff;
            border-top: 1.5em solid #f5f8ff;
          }
          &:after {
            content: '';
            display: inline-block;
            position: absolute;
            bottom: 0;
            right: 0;
            border: 1.5em solid transparent;
            border-right: 1.5em solid #f5f8ff;
            border-bottom: 1.5em solid #f5f8ff;
          }
          .federated_learning_common_separator {
            &:after {
              margin: 2.4em 0;
            }
            &:last-child:after {
              content: none;
            }
          }
          .item {
            .item_title {
              margin-bottom: 1.6em;
            }
          }
        }
      }
    }
    .basic_principles_content_conclusion {
      margin-top: 6.4em;
      .federated_learning_common_grid {
        align-items: start;
      }
    }
  }
}

@media all and (max-width: 1100px){
  .federated_learning_content_federated {
    .federated_learning_common_grid {
      gap: 3.2em !important;
      grid-template-columns: auto !important;
    }
    .federated_learning_content_federated_list_item_separator div {
      position: static !important;
      transform: translateX(0) !important;
    }
  }
  .basic_principles_content_card_item:first-child {
    margin-top: 0;
  }
  .basic_principles_content_card_item {
    margin-top: 7.2em;
  }
  .federated_learning_common {
    .custom_coll_div p:last-child {
      margin-bottom: 1.01em;
    }
  }
  .federated_learning_common  {
    .federated_learning_common_img_left {
      padding-right: 0;
    }
    .federated_learning_common_left {
      margin-left: 0;
    }
    .federated_learning_common_grid {
      grid-template-columns: auto;
      gap: 1.6em;
      .coll {
        max-width: none;
        margin: 0 auto;
      }
    }
  }
  .federated_learning_common img {
    max-width: 52em;
  }
}

@media all and (max-width: 700px) {
  .federated_learning .federated_learning_title {
    max-width: none;
  }
  .federated_learning_common .federated_learning_common_left {
    max-width: none;
  }
  .federated_learning_common {
    section {
      margin-bottom: 4.32em;
    }

    .text_l {
      font-size: 3.6em;
    }

    .text_ls {
      font-size: 2.733em;
    }

    .federated_learning_common_grid .custom_coll_div {
      div {
        margin-bottom: 0;
      }
      p:last-child {
         margin-bottom: 1.01em;
      }
    }
    .mb-120 {
      margin-bottom: 4.4em;
    }
    .federated_learning_content {
      .mb-120:first-child {
        margin-bottom: 0;
      }
    }
    .basic_principles {
      .page_title_block {
        padding-top: 0;
      }
    }
  }
  .basic_principles_content_step_list_item_description {
    p {
      margin-bottom: 0;
    }
  }
  .federated_learning_common_separator:last-child {
    &:after {
      margin-bottom: 0;
    }
  }
  .federated_learning_common {
    .federated_learning_common_grid {
      .coll {
        margin: 0;
      }
    }
  }
  .federated_learning_content_federated_right_col {
    strong {
      font-size: 1.2em;
    }
    p:last-child {
      margin-bottom: 0;
    }
  }
  .basic_principles_content_step_list_item_description {
    margin-top: 0 !important;
  }
  .basic_principles_content_step_list {
    margin-bottom: 4.31em;
  }
  .basic_principles_content_card {
    .federated_learning_common_grid {
      gap: 4.3em;
    }
  }
  .basic_principles_content_card_item {
    margin-top: 0 !important;
  }
  .basic_principles_content_card_item_card {
    margin-top: 2.2em !important;
    padding: 3.23em !important;
  }
  .basic_principles_content_conclusion {
    margin-top: 3.3em !important;
    .federated_learning_common_grid {
      gap: 0;
    }
    .coll:last-child {
      p {
        margin-top: 1.01em;
        margin-bottom: 0;
      }
    }
  }
  .federated_learning_common_img_left {
    margin-top: 2.2em;
  }
  .federated_learning_common_separator {
    &:after {
      margin: 3.3em 0 !important;
    }
  }
  .federated_learning {
    .federated_learning_content {
      .federated_learning_content_federated {
        .federated_learning_content_federated_list {
          .federated_learning_content_federated_list_item {
            margin-top: 1.1em;
            flex-direction: column;
            .federated_learning_content_federated_list_item_separator {
              margin-top: 0;
              margin-bottom: 2.2em;
            }
            .federated_learning_content_federated_list_item_element {
              max-width: none;
              order: 1;
              width: auto;
              flex-direction: row;
              text-align: left;
              .text_m {
                margin-left: 1em;
                max-width: 16em;
              }
            }
          }
        }
      }
    }
  }
}

@media all and (max-width: 480px) {
  .subtitle {
    max-width: 40em;
  }
}
