//     : @import './src/less/blocks/feedback.less';


.feedback {
    position: relative;
    padding-top: 8em;
    
    @media all and (max-width: 980px) {
        padding-top: 8em;
    }
    
    .container {
        
        .feedback_inner {
            position: relative;
            padding: 0 0 11.5em 0;
            display: grid;
            grid-template-columns: 1fr 1.5fr;
            
            @media all and (max-width: 480px) {
                grid-template-columns: 1fr;
                grid-gap: 3em;
            }
        }
        .section_title {
            text-transform: uppercase;
            font-size: 6em;
            margin-top: 1.1em;
            margin-bottom: 0.3em;
            
            @media all and (max-width: 980px) {
                font-size: 4.4em;
            }
            @media all and (max-width: 700px) {
                font-size: 4em;
            }
        }
        .line_separator {
            width: ~'calc(100% + 3.1em)';
            grid-column: 1;
            
            @media all and (max-width: 980px) {
                display: none;
            }
        }
        .contact_block {
            display: flex;
            flex-direction: column;
            // margin-top: 9.8em;
            align-items: flex-start;
            
            @media all and (max-width: 480px) {
                // grid-row: 2;
            }
            
            .contact_title {
                font-size: 3em;
                
                @media all and (max-width: 480px) {
                    font-size: 2.6em;
                }
            }
            .mail {
                margin-top: 0.5em;
                .text_ml();
            }
            
            .btn {
                background-color: #f5f5f5;
                margin-top: 3.7em;
                &:before, &:after {
                    background-color: #f5f5f5;
                }
            }
        }
        .form_item {
            width: 55.5em;
            min-height: 51em;
            grid-row: 1 / span 3;
            grid-column: 2;
            z-index: 10;
            display: flex;
            align-items: stretch;
            
            @media all and (max-width: 980px) {
                width: 42em;
            }
            @media all and (max-width: 700px) {
                width: 100% !important;
                margin: 0 auto;
            }
            @media all and (max-width: 480px) {
                margin-top: 2.5em;
                margin-bottom: -1.1em;
                grid-column: initial;
                grid-row: 3;
            }

            .hbspt-form {
                height: 100%;
                width: 100%;

                // .hs-form-iframe {
                //     width: 100%;
                //     height: 100% !important;
                // }
            }
        }
    }
}
