.fully_homomorphic_common {
  margin-bottom: 6.4em;
  .schemes_rsa {
    padding: 0;
    .inner {
      padding: 7.5em 15em;
    }
    h2 {
      max-width: 20em;
    }
  }
  .mb-120 {
    margin-bottom: 7.2em;
  }
  .text_m p {
    margin-bottom: 1.01em;
  }
  .mb-55 {
    margin-bottom: 5.5em;
  }
  a {
    color: @purple;
  }
  img {
    width: 100%;
    height: auto;
    will-change: transform;
  }
  .fully_homomorphic_common_content_left {
    margin-left: 11em;
  }
  h2, h1 {
    span {
      color: @purple;
    }
  }
  h2.text_l {
    span {
      color: #C9C2F9;
    }
  }
  h2.text_xl {
    color: @lavender;
    span {
      //color: @white;
      color: #C9C2F9;
    }
  }
  h2.text_ls {
    max-width: 17.5em;
    width: 100%;
    line-height: 1;
    position: relative;
    left: -3.66666em;
    margin-bottom: 1.333333em;
  }

  h1.text_4xl {
    line-height: 1;
  }
  .encrypted_data_h2_no_left {
    left: 0 !important;
    margin-left: 0;
  }
}
.fully_homomorphic_encryption {
  h1 {
    line-height: 1em;
  }
  .text_page_title {
    max-width: 15em;
  }
  .fully_subtitle {
    max-width: 430px;
    width: 100%;
    font-weight: 400;
    color: #252C32;
  }
  .fully_separator {
  }
  .fully_content {
    display: grid;
    justify-content: space-between;
    align-items: center;
    grid-template-columns: repeat(2, auto);
    gap: 5em;
    img {
      max-width: 52em;
    }
    .text_m {
      max-width: 52em;
    }
    .text_m > div {
      padding: 1.5em 2em;
      background: #776ACA;
      max-width: 32.5em;
      width: 100%;
      height: auto;
      float: right;
      font-weight: 400;
      color: #FFFFFF;
      position: relative;
      &:before {
        content: '';
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        border: .5em solid transparent;
        border-left: .5em solid #f5f8ff;
        border-top: .5em solid #f5f8ff;
      }
      &:after {
        content: '';
        display: inline-block;
        position: absolute;
        bottom: 0;
        right: 0;
        border: .5em solid transparent;
        border-right: .5em solid #f5f8ff;
        border-bottom: .5em solid #f5f8ff;
      }
    }
  }
}

.encrypted_data {
  display: grid;
  align-items: end;
  justify-content: space-between;
  grid-template-columns: repeat(2, auto);
  gap: 5em;
  .encrypted_data_content {
    max-width: 52em;
    width: 100%;
    img {
      max-width: 51.8em;
    }
  }
}

.schemes_rsa {
  .inner {
    padding: 12em 17.3em 12em 14em;
    .section_title {
      text-align: left;
    }
    .list {
      .item {
        grid-template-columns: auto;
        .text_m {
          font-weight: 400;
          span {
            font-weight: 600;
          }
          strong {
            font-style: italic;
            font-weight: 600;
          }
        }
      }
    }
  }
}
.basic_operations_with_encrypted_data {
  display: grid;
  align-items: center;
  justify-content: space-between;
  grid-template-columns: repeat(2, auto);
  gap: 5em;
  .basic_operations_with_encrypted_data_content {
    max-width: 52em;
    .text_m {
      div {
        color: @white;
        background: @violet;
        padding: .8em 1.5em;
        font-weight: 400;
        strong {
          font-weight: 600;
        }
      }
    }
  }
  .basic_operations_with_encrypted_data_content_repeat {
    max-width: 59.5em;
    height: 100%;
    position: relative;
    .basic_operations_with_encrypted_data_content_repeat_line {
      display: inline-block;
      position: absolute;
      width: .1em;
      height: 100%;
      left: 0;
      top: 50%;
      transform: translate(0, -49%) rotate(180deg);
      .line_separator {
        height: 100%;
        &:before, &:after {
          height: 1em;
        }
        &:before {
          top: 10px;
          left: 50%;
          transform: translateY(0) translateX(-50%) scale(-1, 1) rotate(270deg)
        }
        &:after {
          bottom: 10px;
          left: 50%;
          top: auto;
          transform: translateY(0) translateX(-50%) rotate(90deg)
        }
      }
    }
    .basic_operations_with_encrypted_data_content_repeat_box {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      p {
        margin-bottom: 0;
      }
      .box_line {
        position: relative;
        strong {
          font-size: 1.15em;
        }
        &:after {
          position: relative;
          content: '';
          display: inline-block;
          width: 100%;
          height: 3em;
          bottom: auto;
          left: -.4em;
          background-image: url("../img/vector_horizont.svg");
          background-size: contain;
          background-repeat: no-repeat;
          background-position: center;
          margin-bottom: 2.4em;
        }
        &:last-child:after {
          content: none;
        }
      }
      .item {
        padding-left: 6.5em;
      }
    }
  }
}
@media all and (max-width: 1380px) {
  .basic_operations_with_encrypted_data .basic_operations_with_encrypted_data_content_repeat .basic_operations_with_encrypted_data_content_repeat_box .box_line:after {
    left: -.8em;
  }
}
@media all and (max-width: 1100px) {
  .basic_operations_with_encrypted_data .basic_operations_with_encrypted_data_content_repeat .basic_operations_with_encrypted_data_content_repeat_box .box_line:after {
    left: -0.3em;
  }
  .fully_homomorphic_common {
    .fully_homomorphic_common_content_left {
      margin-left: 0;
    }
    h2.text_ls {
      left: 0;
    }
  }
}
@media all and (max-width: 980px){
  .fully_homomorphic_common {
    .schemes_rsa {
      .inner {
        padding: 11em var(--container_padding);
      }
    }
  }
  .basic_operations_with_encrypted_data {
    gap: 4em;
    .basic_operations_with_encrypted_data_content {
      max-width: none;
    }
    .basic_operations_with_encrypted_data_content_repeat {
      max-width: none;
      .basic_operations_with_encrypted_data_content_repeat_line {
        display: inline-block;
        position: absolute;
        width: 100%;
        height: .1em;
        left: 0;
        top: 0;
        transform: translate(0, 0) rotate(0);
        .line_separator:after {
          bottom: auto;
          left: auto;
          top: auto;
          transform: translateY(-50%);
        }
        .line_separator:before {
          top: auto;
          left: auto;
          transform: translateY(-50%) scale(-1, 1);
        }
      }
      .basic_operations_with_encrypted_data_content_repeat_box {
        margin-top: 4.5em;
        height: auto;
        .box_line {
          margin-bottom: 2.4em;
          .item {
            padding-left: 0;
          }
        }
        .box_line:after {
          content: none;
        }
      }
    }
  }

  .fully_homomorphic_encryption {
    .fully_content {
      grid-template-columns: auto;
      .text_m {
        max-width: none;
        div {
          max-width: none;
        }
      }
    }
  }
  .encrypted_data {
      grid-template-columns: auto;
    .encrypted_data_content {
      max-width: none;
    }
  }
  .basic_operations_with_encrypted_data {
    grid-template-columns: auto;
  }
}

@media all and (max-width: 700px) {
  .fully_homomorphic_common {
    .mb-120 {
      margin-bottom: 3.3em;
    }
    .text_ls {
      font-size: 2.7em;
    }
    .text_l {
      font-size: 3.6em;
    }
    .schemes_rsa {
      .inner {
        padding: 4.32em var(--container_padding);
      }
    }
  }
  .basic_operations_with_encrypted_data_content_repeat_line {
      .line_separator {
        height: 1px !important;
      }
  }
  .fully_homomorphic_common h2.text_ls {
    margin-bottom: .65em;
  }
  .mb-120 {
    margin-bottom: 3.25em;
  }
  .fully_homomorphic_encryption .fully_content, .encrypted_data {
    gap: 0;
  }
  .fully_homomorphic_encryption .about_list, .schemes_rsa {
    padding-top: 0;
    padding-bottom: 3.82em;
    .inner {
      padding: 4.32em var(--container_padding);

      &:before {
        border: 1em solid transparent !important;
        border-left: 1em solid #f5f8ff !important;
        border-top: 1em solid #f5f8ff !important;
      }
      &:after {
        content: '';
        display: inline-block;
        width: 16em;
        height: .4em;
      }
      .section_title {
        margin-bottom: .92em;
      }
      .list {
        padding-top: 1em;
        .item {
          padding-bottom: 2.2em;
          padding-left: 2.2em;
        }
      }
    }
  }
  .fully_homomorphic_common .mb-55 {
    margin-bottom: 0;
    margin-top: 1.09em;
  }
  .basic_operations_with_encrypted_data_content h2 {
    margin-bottom: 1em;
  }
  .basic_operations_with_encrypted_data_content_repeat_box {
    margin-top: 3.8em;
    p:last-child, .box_line:last-child {
      margin-bottom: 0 !important;
    }
  }
}

@media all and (max-width: 480px){
  .fully_homomorphic_common {
    .about_list .inner .list .item, .schemes_rsa .inner .list {
      .item {
        margin-left: 0.6em !important;
      }
      &:before {
        left: auto;
        right: ~'calc(100% - 0.6em) !important';
      }
      &:after {
        left: auto;
        right: ~'calc(100% - 0.5em) !important';
      }
    }
  }
}
