.demo_features {
    padding-bottom: 4.8em;
    @media all and (max-width: 480px) {
        padding-bottom: 1.5em;
    }
    .demo_features_inner {
        display: grid;
        grid-template-columns: 41em 1fr;
        grid-gap: 3em;
        max-width: 118em;
        margin-left: auto;
        margin-right: auto;
        
        @media all and (max-width: 980px) {
            grid-template-columns: 1fr;
        }
        
        .scheme_block {
            width: 41em;
            height: 50.5em;
            position: relative;
            
            @media all and (max-width: 980px) {
                margin-left: auto;
                margin-right: auto;
            }
            @media all and (max-width: 700px) {
                font-size: 8px;
            }
            @media all and (max-width: 480px) {
                font-size: 6.8px;
            }
            
            .img_wrapper {
                width: 40.9em;
                height: 40.9em;
                position: absolute;
                top: 4.5em;
                left: 0;
                z-index: 1;
            }
            .line {
                display: inline-block;
                background-size: contain;
                background-repeat: no-repeat;
                background-position: center;
                position: absolute;
                z-index: 2;
                opacity: 0;
                visibility: hidden;
                transition: .3s;
            }
            
            .inner {
                background: rgba(255,255,255,.25);
                box-shadow: 0px 21.59px 30.85px 0px #00000033;
                width: 5em;
                height: 5em;
                transition: .3s;
                position: absolute;
                border-radius: 50%;
                transform: translate(-50%, -50%);
                z-index: 3;
                cursor: pointer;
                
                // @media all and (max-width: 700px) {
                //     font-size: 1.5em;
                // }
            }
            
            .number_item {
                position: absolute;
                top: 50%;
                left: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                font-family: 'Orbitron';
                transform: translate(-50%, -50%);
                border-radius: 50%;
                background: @white;
                font-size: 1.6em;
                width: 2.25em;
                height: 2.25em;
                transition: .3s;
                @media all and (max-width: 480px) {
                    // font-size: 2em;
                }
            }
            
            .text_item {
                position: absolute;
                text-align: center;
                color: #fff;
                font-size: 1.6em;
                padding: 0.6em .8em;
                background: #776ACA;
                width: 9em;
                opacity: 0;
                visibility: hidden;
                transition: .3s;
                z-index: 10;
                
                @media all and (max-width: 480px) {
                    font-size: 2em;
                }
            }
            
            
            .text_item_1 { top: 0; left: 0; }
            .text_item_2 { top: 0; right: 0; }
            .text_item_3 { bottom: 0; left: 0; }
            .text_item_4 { bottom: 0; right: 0; }
            
            
            .inner_1 {
                top: 17.2em;
                left: 13.7em;
            }
            .inner_2 {
                top: 13.1em;
                left: 31.3em;
            }
            .inner_2.pulse {
                animation: circle 3s linear infinite;
            }
            .inner_3 {
                top: 39.2em;
                left: 13.7em;
            }
            .inner_4 {
                top: 35.7em;
                left: 31.4em;
            }
            
            
            
            .line_1 {
                width: 7.2em;
                height: 8.6em;
                background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iNzAiIGhlaWdodD0iODMiIHZpZXdCb3g9IjAgMCA3MCA4MyIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KPHBhdGggZD0iTTEgMC41VjU4TDY5IDU3LjY2MDZWODIuNSIgc3Ryb2tlPSIjNzc2QUNBIiBzdHJva2Utd2lkdGg9IjIiLz4KPC9zdmc+Cg==");
                top: 5.5em;
                left: 6.3em;
            }
            .line_2 {
                width: 5.75em;
                height: 8.0em;
                background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iNTkiIGhlaWdodD0iODIiIHZpZXdCb3g9IjAgMCA1OSA4MiIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KPHBhdGggZD0iTTU4IDAuNVY4MC41SDAuNSIgc3Ryb2tlPSIjNzc2QUNBIiBzdHJva2Utd2lkdGg9IjIiLz4KPC9zdmc+Cg==");
                top: 5.4em;
                left: 34.2em;
            }
            .line_3 {
                width: 10.6em;
                height: 4.6em;
                background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTA3IiBoZWlnaHQ9IjQ3IiB2aWV3Qm94PSIwIDAgMTA3IDQ3IiBmaWxsPSJub25lIiB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciPgo8cGF0aCBkPSJNMTA3IDFIMVY0NyIgc3Ryb2tlPSIjNzc2QUNBIiBzdHJva2Utd2lkdGg9IjIiLz4KPC9zdmc+Cg==");
                top: 40.4em;
                left: 2.9em;
            }
            .line_4 {
                width: 4.7em;
                height: 8.6em;
                background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iNDkiIGhlaWdodD0iODYiIHZpZXdCb3g9IjAgMCA0OSA4NiIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KPHBhdGggZD0iTTEgMFY0NC4xMzE2SDQ4Vjg2IiBzdHJva2U9IiM3NzZBQ0EiIHN0cm9rZS13aWR0aD0iMiIvPgo8L3N2Zz4K");
                top: 36.6em;
                left: 31.3em;
            }
            
            
            
            .inner.active {
                box-shadow: 0px 33.3px 47.58px 0px #00000033;
                transform: translate(-50%, -50%) scale(1.2);
                
                .number_item {
                    background: #776aca;
                    color: #fff;
                }
            }
            
            .line.active {
                opacity: 1;
                visibility: visible;
            }
            .number.active {
                background: @violet;
                color: @white;
            }
            .text_item.active {
                opacity: 1;
                visibility: visible;
            }
            
            
        }
        
        .text_block {
            
            p {
                .text_md();
                line-height: 1.3;
                margin-bottom: 1.6em;
                
                @media all and (max-width: 700px) {
                    line-height: 1.5;
                    margin-bottom: 1.3em;
                }
            }
            
            table {
                font-size: 1.6em;
                width: 100%;
                margin-bottom: 1.4em;
                
                @media all and (max-width: 700px) {
                    display: flex;
                    flex-direction: row;
                    font-size: 1.778em;
                }
                @media all and (max-width: 480px) {
                    font-size: 2.13121em;
                }
                thead,
                tbody {
                    @media all and (max-width: 700px) {
                        width: 50%;
                    }
                }
                thead tr,
                tbody tr {
                    @media all and (max-width: 700px) {
                        display: flex;
                        flex-direction: column;
                    }
                }
                th, td {
                    width: 30%;
                    text-align: left;
                    line-height: 1.5;
                    
                    @media all and (max-width: 700px) {
                        width: 100% !important;
                        padding: 0.5em 0;
                    }
                    @media all and (max-width: 480px) {
                        padding: 0.4em 0;
                    }
                    
                    &:last-child {
                        width: 40%;
                    }
                }
                td {
                    padding: 0.5em 0;
                }
            }
            h2 {
                @media all and (max-width: 480px) {
                    font-size: 2.7em;
                }
            }
            h2, h3, h4 { 
                .ff_secondary();
                margin-bottom: 0.8em;
            }
            
        }
    }
}

.steps_block {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    align-items: center;
    grid-gap: 3.2em 0;
    padding-left: 5.4em;
    padding-bottom: 7em;
    
    @media all and (max-width: 1100px) {
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 2em 0;
    }
    @media all and (max-width: 980px) {
        grid-template-columns: 1fr 1fr;
    }
    @media all and (max-width: 700px) {
        grid-template-columns: 1fr;
        grid-gap: 0;
        padding-bottom: 3.5em;
    }
    
    .title_wrapper {
        grid-column: 1 / span 2;
        margin-left: -5.4em;
        
        @media all and (max-width: 700px) {
            grid-column: initial;
            padding-bottom: 2.8em;
        }
        
        .section_title {
            padding-right: 3em;
            
            @media all and (max-width: 980px) {
                padding-right: 0;
            }
            @media all and (max-width: 480px) {
                font-size: 3.5em;
                line-height: 1.2;
            }
        }
    }
    .step_item {
        position: relative;
        margin: 0 0em 0 -5.4em;
        
        &:last-of-type {
            .step_inner {
                @media all and (max-width: 480px) {
                    padding-bottom: 3em;
                }
                &:after {
                    display: none;
                }
                &:before {
                    @media all and (max-width: 480px) {
                        border: none;
                    }
                }
            }
        }
        
        .step_inner {
            display: flex;
            flex-direction: column;
            gap: 1.2em;
            position: relative;
            background: #fff;
            padding: 1.6em 0em 1.6em 7.8em;
            width: ~'calc(100% - 5.4em)';
            
            
            @media all and (max-width: 1380px) {
                padding-left: 6.5em;
            }
            @media all and (max-width: 1100px) {
                padding-left: 7.5em;
            }
            @media all and (max-width: 700px) {
                flex-direction: row;
                padding: 1.8em 0;
                width: 100%;
                align-items: center;
                margin-bottom: 1px;
            }
            @media all and (max-width: 480px) {
                padding: 3em 0 1em;
            }
            
            
            &:before,
            &:after {
                content: '';
                display: inline-block;
                height: 100%;
                width: 5.4em;
                position: absolute;
                top: 0;
                background-size: contain;
                background-repeat: no-repeat;
                background-position: center;
            }
            &:before {
                background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iNTQiIGhlaWdodD0iMTg5IiB2aWV3Qm94PSIwIDAgNTQgMTg5IiBmaWxsPSJub25lIiB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciPgo8cGF0aCBkPSJNMCAwSDU0VjE4OUgwTDU0IDk0LjVMMCAwWiIgZmlsbD0id2hpdGUiLz4KPC9zdmc+Cg==");
                left: 0;
                background-color: #f5f8ff;
                
                @media all and (max-width: 700px) {
                    border-bottom: 1px solid @purple;
                    background: #fff;
                    background-image: none;
                    width: 100vw;
                    height: 100%;
                    top: 0;
                    left: 50%;
                    transform: translateX(-50%);
                    z-index: -1;
                }
            }
            &:after {
                background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iNTYiIGhlaWdodD0iMTkxIiB2aWV3Qm94PSIwIDAgNTYgMTkxIiBmaWxsPSJub25lIiB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciPgo8cGF0aCBkPSJNMSAxTDU1IDk1LjVMMSAxOTBWMVoiIGZpbGw9IndoaXRlIi8+CjxwYXRoIGQ9Ik0xIDFMNTUgOTUuNUwxIDE5MCIgc3Ryb2tlPSIjOTU4OEU4Ii8+Cjwvc3ZnPgo=");
                left: ~'calc(100% - 1px)';
                @media all and (max-width: 700px) {
                    background-image: url("data:image/svg+xml;base64,");
                    width: 375px;
                    height: 11px;
                    top: 100%;
                    left: -3.4em;
                }
                @media all and (max-width: 480px) {
                    left: -5.3em;
                }
            }
        }
        
        
        .icon_wrapper {
            width: 6.7em;
            height: 6.7em;
            
            @media all and (max-width: 700px) {
                width: 5em;
                height: 5em;
            }
        }
        .number {
            font-size: 3em;
            .ff_secondary();
            font-weight: normal;
            line-height: 1.4;
            @media all and (max-width: 700px) {
                margin-left: auto;
            }
            @media all and (max-width: 480px) {
                font-size: 2.3em;
            }
        }
    }
}


@keyframes circle {
    0% { transform: translate(-50%, -50%) scale(1); }
    40% { transform: translate(-50%, -50%) scale(1); }
    50% { transform: translate(-50%, -50%) scale(1.1); }
    60% { transform: translate(-50%, -50%) scale(1); }
    70% { transform: translate(-50%, -50%) scale(1.1); }
    80% { transform: translate(-50%, -50%) scale(1); }
    100% { transform: translate(-50%, -50%) scale(1); }
}


.demo_form {
    display: grid;
    grid-template-columns: 1fr 2.08fr;
    grid-gap: 3.0em;
    padding-bottom: 4.8em;
    
    @media all and (max-width: 980px) {
        grid-template-columns: 1fr;
    }
    
    .text_block {
        .section_title {
            margin-bottom: 0.6em;
            
            @media all and (max-width: 480px) {
                font-size: 3.5em;
            }
        }
        .text_m {
            p {
                margin-bottom: 0.8em;
                line-height: 1.3;
            }
        }
    }
    .form_wrapper {
        
    }
}
