//     : @import '../_source_dev/less/blocks/benefits.less';


.benefits {
    padding: 5.6em 0;


    .text_box {
        margin: 5em 0 4em 44em;
        max-width: 43em;
        
        @media all and (max-width: 980px) {
            margin: 3em 0 4em 12em;
        }
        @media all and (max-width: 700px) {
            margin-left: 0;
        }
        @media all and (max-width: 480px) {
            max-width: 100%;
            margin-bottom: 5.7em;
        }
    }
    
    .benefit_list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 3em;
        margin-bottom: 4.4em;
        
        @media all and (max-width: 1100px) {
            grid-gap: 2em;
        }
        
        @media all and (max-width: 980px) {
            grid-template-columns: 1fr 1fr;
            grid-gap: 2em;
        }

        @media all and (max-width: 480px) {
            grid-template-columns: 1fr;
        }
        
        .benefit_card_item {
            border: 1px solid #776ACA;
            background: #fff;
            padding: 2.4em 3.2em;
            display: grid;
            grid-template-columns: 1fr;
            grid-gap: 3em;
            
            @media all and (max-width: 1100px) {
                grid-gap: 2em;
            }
            
            @media all and (max-width: 980px) {
                grid-gap: 4em;
            }
            @media all and (max-width: 700px) {
                display: flex;
                flex-direction: column;
                padding: 2em;
            }
            @media all and (max-width: 480px) {
                padding: 3.9em 2.6em;
                grid-gap: 2em;
            }
            
            .label {
                color: #9588E8;
                border: 1px solid #9588E8;
                padding: 0.8em 1.6em;
                display: flex;
                justify-content: center;
                align-items: center;
                margin-left: auto;
                height: 3.2em;

                @media all and (max-width: 980px) {
                    
                }
                @media all and (max-width: 700px) {
                    grid-row: initial;
                    grid-column: initial;
                    margin-bottom: 1em;
                }
                
                .text_m { line-height: 1.1; }
            }
            .text_m {
                line-height: 1.3;

                @media all and (max-width: 980px) {
                    
                }
                @media all and (max-width: 700px) {
                    grid-row: initial;
                    grid-column: initial;
                }
            }
            .number {
                font-weight: 400;
                font-size: 7.4em;
                .ff_secondary();
                line-height: 1.4;
            }
        }
    }
}
