.block_shield {
  width: 100%;
  .block_shield_title {
    display: flex;
    align-items: center;
    justify-content: center;
    h2 {
      font-family: 'Orbitron', sans-serif;
      font-weight: 600;
      //font-size: 2.8em;
      line-height: 140%;
      color: #252C32;
      margin-bottom: 1.44em;
      span {
        color: #9588e8;
      }
    }
  }
  .block_shield_content {
    display: grid;
    justify-content: space-between;
    grid-auto-columns: auto;
    grid-template-columns: repeat(auto-fit, minmax(15em, 1fr));
    align-items: start;
    @media all and (max-width: 1100px) { 
      grid-template-columns: repeat(auto-fit, minmax(20em, 1fr));
    }
    @media all and (max-width: 980px) { 
      grid-template-columns: repeat(auto-fit, minmax(15em, 1fr));
    }
    @media all and (max-width: 700px) { 
      display: block;
    }
    .block_shield_content_item {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      text-align: center;
      @media all and (max-width: 700px) { 
        align-items: start;
        display: grid;
        grid-template-columns: repeat(2, auto);
        justify-content: start;
      }
      .block_shield_content_item_figure {
        display: none;
      }
      &:last-child {
        position: relative;
        .block_shield_content_item_figure {
          display: block;
          position: absolute;
          left: -6em;
          bottom: -1em;
          @media all and (max-width: 700px) {  
            left: auto;
            right: -1em;
            bottom: auto;
            top: -3.5em;
            transform: rotate(270deg);
          }
          &:before {
            content: '';
            position: absolute;
            display: block;
            width: 12em;
            height: 1em;
            background: url(../img/figure1.png) no-repeat;
            scale: 2;
            background-size: contain;
            @media all and (max-width: 700px) {  
              width: 9em;
              bottom: 3em;
              right: -4.5em;
            }
          }
         
          .block_shield_content_item_figure_text {
            .text_m();
            position: absolute;
            display: block;
            width: 12em;
            height: 1em;
            .text_m();
            left: -2.4em;
            bottom: -2em;
            @media all and (max-width: 700px) {  
              font-size: 1.6em;
              left: auto;
              right: -6.3em;
              top: auto;
              transform: translateY(-50%) rotate(-180deg);
              bottom: -0.3em;
            }
          }
        }
        .block_shield_content_item_img:before {
          content: none;
        }
      }
     
      .block_shield_content_item_img {
        display: grid;
        align-items: start;
        justify-content: space-between;
        grid-template-columns: 1fr auto;
        width: 100%;
        div {
          position: relative;
          max-width: 79px;
          width: 100%;
          margin: 0 auto;
          img {
            will-change: transform;
          }
        }
        &:before {
          content: '';
          display: inline-block;
          background: url(../img/arrow_encript.png) no-repeat;
          background-size: contain;
          width: 3em;
          transform: rotate(270deg);
          height: 3em;
          order: 2;
        }
        @media all and (max-width: 700px) { 
          align-items: center;
          justify-content: start;
          grid-template-columns: auto;
          grid-template-rows: auto auto;
          width: auto;
          &:before {
            transform: rotate(0);
            margin: 1.5em 0 1.5em 6em;
            width: 100%;
            height: 4em;
          }
        }
      }
      .block_shield_content_item_text {
        max-width: 9em;
        font-family: 'Inter Tight', sans-serif;
        font-weight: 400;
        color: #252C32;
        span {
          display: block;
          color: #9588e8;
        }
        @media all and (max-width: 700px) {  
          max-width: 13em;
          margin-left: 1.4em;
          text-align: left;
          span {
            display: inline;
          }
        }
      }
    }
  }
}


.column_text_and_two_img {
  .column_text_and_two_img_content:last-child {
    margin-bottom: 0 !important;
  }
  .column_text_and_two_img_content {
    margin-top: 3.2em;
    margin-bottom: 7.2em;
    @media all and (max-width: 980px) { 
      margin: 4.5em 0;
    }
    @media all and (max-width: 700px) { 
      margin-top: 1.1em;
      margin-bottom: 0;
    }
    .column_text_and_two_img_content_text {
      max-width: 40em;
      width: 100%;
      padding-left: 6.9em;
      @media all and (max-width: 980px) { 
        padding-left: 0;
        max-width: none;
      }
      p {
        font-weight: 400;
        color: #252C32;
        margin-bottom: 1.5em;
        @media all and (max-width: 700px) { 
          margin-bottom: 1.1em;
        }
        span, a {
          color: rgba(149, 136, 232, 1);
        }
      }
      p:last-child {
        margin-bottom: 0;
      }
      div {
        padding: 0.688em 1.5em;
        gap: 1.6em;
        background: #776ACA;
        font-weight: 600;
        color: #FFFFFF;
        margin-bottom: 1em;
        @media all and (max-width: 700px) { 
          margin-bottom: 1.1em;
        }
      }
    }
    .column_text_and_two_img_content_wrapper {
      margin-top: 3.2em;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: start;
      justify-content: space-between;
      @media all and (max-width: 980px) { 
        margin-top: 2.3em;
        grid-template-columns: auto;
        gap: 2.4em;
      }
      @media all and (max-width: 700px) { 
        gap: 0;
      }
      .column_text_and_two_img_content_wrapper_item {
        width: 100%;
        height: 100%;
        .column_text_and_two_img_content_wrapper_item_img {
          max-width: 630px;
          will-change: transform;
          @media all and (max-width: 700px) { 
            margin-bottom: 3.24em;
            margin-top: 1.1em;
          }
        }
        .column_text_and_two_img_content_wrapper_item_text {
          margin-top: .75em;
          font-weight: 600;
          text-align: center;
          color: #252C32;
          @media all and (max-width: 700px) { 
            margin-top: 0;
            margin-bottom: 1.2em;
          }
        }
      }
    }
  }
}


.template_grid_img_content {
  .template_grid_img_content_list {
    .template_grid_img_content_list_carusel {
      display: none;
      width: 100%;
      @media all and (max-width: 980px) { 
        display: block;
      }
      .template_grid_img_content_list_carusel_item {
        width: 100%;
      }
    }
    .template_grid_img_content_list_grid {
      display: grid;
      grid-template-columns: repeat(3, auto);
      align-items: end;
      justify-content: space-between;
      @media all and (max-width: 1100px) { 
        grid-template-columns: repeat(2, auto);
      }
      @media all and (max-width: 980px) { 
        display: none;
        opacity: 0;
        pointer-events: none;
      }
      .template_grid_img_content_list_grid_item {
        max-width: 41em;
        width: 100%;
      }
    }
  }
}

.template_table {
  .template_table_pm {
    margin-bottom: 7.2em;
    @media all and (max-width: 980px) {
      margin-bottom: 8.9em;
    }
    @media all and (max-width: 700px) {
      margin-bottom: 4.33em;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  .template_table_text {
    padding-left: 7em;
    max-width: 40em;
    width: 100%;
    .text_m();
    margin-bottom: 2em;
    font-weight: 400;
    @media all and (max-width: 1380px) { 
      padding-left: 0;
      max-width: 34em;
    }
  }
  .template_table_table {
    padding: 0 11em;
    @media all and (max-width: 1380px) { 
      padding: 0;
    }
    table {
      @media all and (max-width: 1380px) { 
        width: 100%;
      }
      th, td {
        border: 1px solid #f5f8ff;
      }
      th {
        background-color: #C9C2F9;
        width: 13.2em;
        padding-top: 1em;
        padding-bottom: 1em;
        font-weight: 400;
        text-align: center;
      }
      span {
        .text_m();
        line-height: normal;
      }
      td {
        background-color: #FFFFFF;
        span {
          .text_m();
          line-height: normal;
          display: flex;
          align-items: center;
          justify-content: center;
          padding-top: .5em;
          padding-bottom: .5em;
        }
      }
    }
    @media all and (max-width: 980px) { 
      table {
        tbody {
          display: grid;
          grid-template-columns: auto auto auto;
          tr {
            display: flex;
            flex-direction: column;
            align-items: unset;
            justify-content: center;
            td, th {
              width: auto;
              height: 100%;
              min-height: 6em;
              display: flex;
              align-items: center;
              justify-content: center;
            }
          }
        }
      }
    }
  }
  .template_table_additional_information {
    display: grid;
    align-items: start;
    justify-content: space-between;
    padding: 0 7em;
    grid-template-columns: repeat(2, 1fr);
    .text_m();
    font-weight: 400;
    gap: 2.15em;
    @media all and (max-width: 1380px) { 
      padding: 0;
    }
    @media all and (max-width: 1100px) { 
      padding: 0;
    }
    @media all and (max-width: 980px) { 
      grid-template-columns: auto;
    }
    .template_table_additional_informationt_righ {
      .template_table_additional_informationt_purple {
        background: url(../img/path.png) no-repeat right #776ACA;
        background-size: contain;
      }
    }
  }
}

.template_table_additional_informationt_purple {
  padding: 1.5em 2em;
  background: #776ACA;
  width: 100%;
  height: auto;
  font-weight: 400;
  color: #FFFFFF;
  position: relative;
  &:before {
    content: '';
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    border: .5em solid transparent;
    border-left: .5em solid #f5f8ff;
    border-top: .5em solid #f5f8ff;
  }
  &:after {
    content: '';
    display: inline-block;
    position: absolute;
    bottom: 0;
    right: 0;
    border: .5em solid transparent;
    border-right: .5em solid #f5f8ff;
    border-bottom: .5em solid #f5f8ff;
  }
}

.img_description {
  display: grid;
  grid-template-columns: repeat(2, auto);
  gap: 11em;
  @media all and (max-width: 980px) {
    grid-template-columns: auto;
    gap: 5em;
    justify-content: center;
  }
  .img_description_item {
    .img_description_item_img {
      max-width: 59em;
      width: 100%;
    }
    .img_description_item_text {
      margin-top: .72em;
      text-align: center;
      .text_s();
      font-weight: 600;
    }
  }
}