header.abs {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 100;
    width: 100%;
}
header.main_page {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 100;
    width: 100%;
    
    .main_menu li a {
        color: @white;
    }
    
    .container {
        @media all and (max-width: 480px) {
            padding-top: 1em;
            padding-bottom: 1em;
        }
    }
    
    .main_menu.lang_menu .menu > .menu-item > a:before {
        background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTIiIGhlaWdodD0iMTMiIHZpZXdCb3g9IjAgMCAxMiAxMyIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KPHBhdGggZD0iTTUuMTMyMDMgMTBMMS40OTQ3MyA1LjVMOC43NjkzNCA1LjVMNS4xMzIwMyAxMFoiIGZpbGw9IiNGNUY1RjUiLz4KPC9zdmc+Cg==");
    }
    
    .burger {
        background-color: @white;
        
        &:after {
            background-color: @white;
        }
        &:before {
            background-color: @white;
        }
    }
}
header.fixed:not(.main_page) {
    @media all and (max-width: 700px) {
        // position: sticky !important;
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        background: #f5f8ff;
        z-index: 9999;
    }
}
header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 99;
    
    .container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding-top: 1.6em;
        padding-bottom: 1.6em;
        
        > .main_menu.lang_menu {
            @media all and (max-width: 980px) {
                display: none;
            }
        }
    }
    
    .logo {
        width: 15.4em;
        height: auto;
        position: relative;
        
        @media all and (max-width: 700px) {
            width: 14em;
        }
    }
    
    .mob_block {
        display: none;
        
        @media all and (max-width: 980px) {
            display: inline-block;
            padding-bottom: 10em;
        }
        
        .main_menu {
            
        }
    }
    .menu_wrapper.visible {
        right: 0;
    }
    .menu_wrapper {
        margin-left: auto;
        @media all and (max-width: 980px) {
            margin-right: 0;
            // display: none;
            position: absolute;
            top: 0;
            right: -100vw;
            background: #fff;
            width: 100vw;
            padding: 8em 3em;
            padding-right: ~'calc((100vw - var(--container_width)) / 2 + var(--container_padding))';
            height: 100vh;
            z-index: 99;
            transition: .3s;
            // background-image: url('../img/menu_bgr.svg');
            background-size: 56.1em;
            background-position: 40% 11.7em;
