                        }
                        .sub-menu {
                            @media all and (max-width: 980px) {
                                height: auto !important;
                                opacity: 1 !important;
                                visibility: visible !important;
                            }
                        }
                    }
                    
                    .sub-menu {
                        @media all and (max-width: 980px) {
