                                visibility: visible;
                            }
                        }
                    }
                }
                .menu-item-89,
                .menu-item-90_off {
                    > .sub-menu {
                        @media all and (max-width: 980px) {
                            width: auto;
                            grid-template-columns: 1fr;
                            position: relative;
                            left: auto;
                        }
                        
                        li.menu-item {
                            @media all and (max-width: 980px) {
                                margin-top: 0;
                                margin-bottom: 0;
                                padding: 1em;
                                grid-column: initial !important;
                                max-width: initial;
                            }
                            
                            a {
                                @media all and (max-width: 980px) {
                                    gap: 1em;
                                }
                                img {
                                    @media all and (max-width: 980px) {
                                        width: 2em;
                                        height: 2em;
                                    }
                                }
                                span {
                                    @media all and (max-width: 980px) {
                                        padding-left: 0;
                                    }
                                }
                            }
                            
                        }
                        li.menu_group_anchor {
                            @media all and (max-width: 980px) {
                                flex-direction: column;
                            }
                            a {
                                @media all and (max-width: 980px) {
                                    background: none !important;
                                    padding: 0;
                                }
                            }
                            > .sub-menu {
                                @media all and (max-width: 980px) {
                                    position: relative;
                                    left: auto;
                                    top: auto;
                                    width: 100%;
                                    grid-template-columns: 1fr;
                                    opacity: 1;
                                    visibility: visible;
                                }
                            }
                        }
                        li.menu_article {
