            }
        }
        .mob_block {
            @media all and (max-width: 980px) {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                gap: 4em;
            }
        }
    }
    .main_menu {
        position: relative;
        padding-left: 2em;
        align-self: stretch;
        display: flex;
        justify-content: center;
        align-items: center;
        
        // > ul {
        //     > li.current-menu-item {
        //         a {
        //             color: #C9C2F9;
        
        //             &:after {
        //                 width: 100%;
        //             }
        //         }
        //         li.current-menu-item {
        //             a {
        //                 color: @violet;
        //             }
        //         }
        //     }
        // }
        
        
        .menu_trigger {
            display: none;
            background: linear-gradient(0deg, #FFFFFF, #FFFFFF);
            -webkit-text-fill-color: transparent;
            background-clip: text;
            -webkit-background-clip: text;
            color: transparent;
            font-size: 1.4em;
            font-weight: 700;
            text-transform: uppercase;
            
            @media all and (max-width: 700px) {
                display: inline-block;
            }
        }
        .menu_trigger.active {
            background: linear-gradient(91.17deg, #C6F1F7 -4.01%, #F983E9 36.14%, #B877FF 77.44%, #C2E9CD 106.11%);
            -webkit-background-clip: text;
            background-clip: text;
            -webkit-text-fill-color: transparent;
            color: @white;
        }
        ul {
            display: flex;
            flex-direction: row;
            gap: 4em;
            
            @media all and (max-width: 1100px) {
                gap: 2em;
            }
            
        }
        ul.visible {
            top: 10em;
            opacity: 1;
            visibility: visible;
            
            @media all and (max-width: 480px) {
                top: 9em;
            }
        }
        > ul > li {
            padding: 1em 0;
            margin: -1em 0;
            
            > a {
                position: relative;
                
                &:after {
                    content: '';
                    display: inline-block;
                    width: 0;
                    height: 1px;
                    background: #c9c2f9;
                    position: absolute;
                    left: 50%;
                    transform: translateX(-50%);
                    bottom: 0;
                    transition: .3s;
                }
            }
        }
        li {
            list-style-type: none;
            padding: 0;
            position: relative;
            
            &:hover {
                
                > a {
                    color: #C9C2F9;
                    
                    &:after {
                        width: 100%;
                    }
                }
                // > .sub-menu {
                //     opacity: 1 !important;
                //     visibility: visible !important;
                // }
            }
            .sub-menu.visible {
                display: flex;
                opacity: 1 !important;
                visibility: visible !important;
                padding-top: 3.2em;
                padding-bottom: 3.2em;
            }
            a {
                font-size: 1.6em;
                font-weight: 700;
                text-transform: uppercase;
                color: @dark;
                transition: background .3s;
            }
            
            .sub-menu {
                position: absolute;
                padding: 1.6em 3.2em;
                background: #fff;
                // top: ~'calc(50% + 4em)';
                top: 5.9em;
                left: -4.7em;
                min-width: 30em;
                display: flex;
                flex-direction: column;
                gap: 0;
                display: none;
                opacity: 0;
                visibility: hidden;
                transition: .3s;
                >li.menu-item {
                    &:hover {
                        background: #F5F5F5;
                    }
                }
                
                .menu-item {
                    padding: 1.6em;
                    border-bottom: 1px solid rgba(110,124,135,.3);
                    position: relative;
                    
                    &:first-child {
                        // margin-top: -1.6em;
                    }
                    
                    &:last-child {
                        border: none;
                        // margin-bottom: -1.6em;
                    }
                    
                    &:after {
                        content: '';
                        display: inline-block;
                        width: 2em;
                        height: 2em;
                        position: absolute;
                        right: 1.6em;
                        top: 50%;
                        transform: translateY(-50%);
                        background-size: contain;
                        background-repeat: no-repeat;
                        background-position: center;
                        background-image: url('../img/arrow-down-right.svg');
                    }
                    
                    a {
                        color: @dark;
                        font-family: 'Inter Tight';
                        font-weight: 400;
                        text-transform: initial;
                        line-height: 1.3;
                        width: 100%;
                        display: inline-block;
                        
                        white-space: nowrap;
                        padding-right: 2em;
                    }
                }
            }
        }
        
        .menu-item-89,
        .menu-item-90_off {
            > .sub-menu.visible {
                display: grid;
            }
            > .sub-menu {
                width: 100vw;
                position: fixed;
                left: 0;
                padding-left: ~'calc((100vw - var(--container_width)) / 2 + var(--container_padding))';
                padding-right: ~'calc((100vw - var(--container_width)) / 2 + var(--container_padding))';
                top: 8em;
                // display: grid;
                grid-template-columns: 30em 1fr;
                grid-gap: 0 14em;
                
                li.menu-image-title-after.menu-item {
                    &:hover {
                        background: none;
                    }
                }
                
                li.menu-item.menu_article {
                    grid-column: 1;
                    grid-row: 1 / span 5;
                    padding: 0;
                    border: none;
                    margin: 0 !important;
                    max-width: initial;
                    
                    &:hover {
                        background: none;
                    }
                    
                    a {
                        .text_ml();
                        font-weight: 600;
                        padding: 0;
                        display: flex;
                        flex-direction: column;
                        white-space: initial;
                        padding-right: 0;
                        
                        img {
                            width: 15em;
                            height: auto;
                            padding-right: 0;
                        }
                        
                        span {
                            padding: 0;
                        }
                    }
                }
                li.menu-item {
                    &:hover {
                        //background: #F5F5F5;
                    }
                }
                li.menu-item {
                    grid-column: 2;
                    grid-row: initial;
                    max-width: 41em;
                    transition: .3s;
                    
                    .sub-menu {
                        opacity: 0;
                        visibility: hidden;
                        
                    }
                    
                    
                    // &:nth-child(2) {
                    //     margin-top: -1.6em;
                    // }
                    
                    &:after {
                        display: none;
                    }
                    
                    a {
                        display: flex;
                        align-items: center;
                        gap: .6em;
                        img {
                            padding: 0;
                            width: 2em;
                            height: 2em;
                        }
                    }
                }
            }
        }
        .menu-item-90_off {
            > .sub-menu {
                grid-template-columns: 41em 1fr;
                grid-gap: 0em 3em;
                grid-template-rows: 7.74em 7.74em;
                
                > li.menu-item {
                    grid-column: 1;
                    display: flex;
                    align-items: center;
                    padding-left: 0;
                    padding-right: 0;
                    &:hover {
                        a {
                            span {
                                &:after {
                                    right: -.3em;
                                }
                            }
                        }
                    }
                    
                    @media all and (max-width: 980px) {
                        grid-column: initial;
                    }
                    
                    > a {
                        padding-left: 0.8em;
                        padding-right: 0.8em;
                        gap: 0.6em;
                        position: relative;
                        
                        &:before {
                            content: '';
                            display: inline-block;
                            position: absolute;
                            top: 0;
                            left: 0;
                            border: .4em solid transparent;
                            border-left: .4em solid #fff;
                            border-top: .4em solid #fff;
                        }
                        &:after {
                            content: '';
                            display: inline-block;
                            position: absolute;
                            bottom: 0;
                            right: 0;
                            width: 0 !important;
                            height: 0 !important;
                            border: .4em solid transparent;
                            border-right: .4em solid #fff;
                            border-bottom: .4em solid #fff;
                        }
                        
                        img {
                            width: 3.5em;
                            height: 3.5em;
                        }
                        span {
                            padding-left: 0;
                            display: inline-block;
                            width: 100%;
                            position: relative;
                            
                            &:after {
                                content: '';
                                display: inline-block;
                                width: 1.2em;
                                height: 1.2em;
                                position: absolute;
                                transition: .2s linear;
                                right: 0em;
                                top: 50%;
                                transform: translateY(-50%);
                                background-size: contain;
                                background-repeat: no-repeat;
                                background-position: center;
                                background-image: url('../img/arrow-right.svg');
                            }
                        }
                    }
                    
                    &:nth-child(2) {
                        > .sub-menu {
                            top: -7.74em;
                        }
                    }
                    
                    >.sub-menu {
                        position: absolute;
                        left: ~'calc(100% + 3em)';
                        top: 0;
                        
                        width: ~'calc(var(--container_width) - 46em - var(--container_padding))';
                        box-sizing: border-box;
                        display: grid;
                        grid-template-columns: 1fr 1fr;
                        grid-gap: 0 3em;
                        
                        .menu-item {
                            transition: .3s;
                            grid-column: initial;
                            border-bottom: 1px solid rgba(110, 124, 135, 0.3);
                            &:hover {
                                background: #F5F5F5;
                            }
                        }
                    }
                }
                > li.menu-item.active {
                    position: relative;
                    z-index: 99;
                    > a {
                        background: #f5f5f5;
                    }
                    >.sub-menu {
                        opacity: 1;
                        visibility: visible;
                    }
                }
            }
            
        }
    }
    
    
    
    .main_menu.lang_menu {
        margin-left: auto;
        padding-right: 4em;
        
        @media all and (max-width: 980px) {
            margin-left: 1em;
            position: relative;
            z-index: 10;
        }
        
        .menu > .menu-item {
            &:hover {
                > a:before {
                    transform: translateY(-50%) rotate(180deg);
                }
            }
            > a {
                position: relative;
                
                @media all and (max-width: 980px) {
                    color: @dark !important;
                    font-size: 2.4em;
                }
                
                &:before {
                    content: '';
                    display: inline-block;
                    width: 0.8em;
                    height: 0.8em;
                    position: absolute;
                    left: 125%;
                    top: 50%;
                    transform-origin: 50% 50%;
                    transform: translateY(-50%);
                    transition: .3s;
                    background-size: contain;
                    background-repeat: no-repeat;
                    background-position: center;
                    background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTMiIGhlaWdodD0iMTMiIHZpZXdCb3g9IjAgMCAxMyAxMyIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KPHBhdGggZD0iTTUuNjMyMDMgMTBMMS45OTQ3MyA1LjVMOS4yNjkzNCA1LjVMNS42MzIwMyAxMFoiIGZpbGw9IiMyNTJDMzIiLz4KPC9zdmc+Cg==");
                    
                    @media all and (max-width: 980px) {
                        background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTMiIGhlaWdodD0iMTMiIHZpZXdCb3g9IjAgMCAxMyAxMyIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KPHBhdGggZD0iTTUuNjMyMDMgMTBMMS45OTQ3MyA1LjVMOS4yNjkzNCA1LjVMNS42MzIwMyAxMFoiIGZpbGw9IiMyNTJDMzIiLz4KPC9zdmc+Cg==");
                    }
                }
            }
            .sub-menu {
                min-width: 22em;
                > li.menu-item {
                    &:after {
                        opacity: 0 !important;
                    }
                    a {
                        @media all and (max-width: 980px) {
                            font-size: 2.4em;
                        }
                    }
                }
                > li.menu-item.active {
                    &:after {
                        opacity: 1 !important;
                        background-image: url("data:image/svg+xml;base64,");
                    }
                }
            }
        }
    }
    
    .container > .btn {
        @media all and (max-width: 480px) {
            display: none;
        }
    }
}


.burger_wrapper {
    display: none;
    position: relative;
    // margin-right: 1.5em;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    height: 2em;
    width: 10.4em;
    font-size: 1em;
    position: relative;
    z-index: 100;
    
    @media all and (max-width: 980px) {
        display: -webkit-flex !important;
        display: -ms-flex !important;
        display: flex !important;
    }
    @media all and (max-width: 700px) {
        width: 6em;
    }
    
    
    .burger {
        display: inline-block;
        width: 1.5em;
        height: 2px;
        background-color: @dark;
        position: relative;
        transition: none;
        border-radius: 2px;
        font-size: 2.2em;
        margin-left: auto;
        
        &:after,
        &:before {
            content: '';
            display: inline-block;
            width: 1.5em;
            height: 2px;
            background-color: @dark;
            position: absolute;
            left: 0;
            transition: .3s;
            border-radius: 2px;
            will-change: transform;
            // transform-origin: 50% 50%;
        }
        &:after {
            top: -.45em;
        }
        &:before {
            bottom: -.45em;
        }
    }
    
    .burger.active {
        height: 0;
        background-color: @dark;
        
        &:after {
            top: -.04em;
            transform: rotate(45deg);
            background-color: @dark;
        }
        &:before {
            bottom: -0.04em;
            transform: rotate(-45deg);
            background-color: @dark;
        }
    }
    
}

