//     : @import '../_source_dev/less/blocks/footer.less';


footer.footer {
    padding: 6em 0 1.6em 0;
    background: @violet;
    color: #fff;
    position: relative;
    z-index: 5;
    overflow: hidden;

    .container {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: space-between;
        position: relative;

        .scroll_btn {
            @media all and (max-width: 700px) {
                display: none;
            }
        }

        .footer_inner {
            display: grid;
            grid-template-columns: 0.8fr 2fr;
            grid-gap: 10em;

            @media all and (max-width: 1380px) {
                grid-gap: 5em;
            }
            @media all and (max-width: 1100px) {
                grid-gap: 3em;
                grid-template-columns: .7fr 2fr;
            }
            @media all and (max-width: 980px) {
                grid-template-columns: 1fr 1.2fr;
            }
            @media all and (max-width: 700px) {
                width: 100%;
            }
            @media all and (max-width: 480px) {
                grid-template-columns: 1fr;
            }

            .contacts {
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: flex-start;

                @media all and (max-width: 980px) {
                    flex-direction: column;
                }
                @media all and (max-width: 700px) {
                    flex-direction: column;
                }
                @media all and (max-width: 480px) {
                    padding-right: 5em;
                }

                .contact_title {
                    font-size: 3em;
                    .ff_secondary();
                    font-weight: 600;
                    text-transform: uppercase;
                    line-height: 1.3;
                    margin-bottom: 1.2em;

                    @media all and (max-width: 1100px) {
                        font-size: 2.2em;
                    }

                    @media all and (max-width: 980px) {
                        max-width: 18em;
                        font-size: 3.3em;
                    }
                }
                .button_block {
                    display: grid;
                    grid-template-columns: 1fr;
                    grid-gap: 1.4em;

                    @media all and (max-width: 980px) {
                        margin-left: 0;
                    }
                    @media all and (max-width: 700px) {
                        margin-left: 0;
                    }
                }
                .btn {
                    width: 15.4em;
                    text-align: center;
                    @media all and (max-width: 980px) {
                        width: 18.4em;
                    }
                    @media all and (max-width: 700px) {
                        width: 18.4em;
                    }
                }
            }
            .menu_wrapper {
                li {
                    list-style-type: none;
                }
                .menu {
                    display: grid;
                    grid-template-columns: 0.6fr 1.5fr 1.4fr 0.9fr;
                    grid-gap: 3.2em;
                    padding-right: 5.5em;

                    @media all and (max-width: 1100px) {
                        grid-template-columns: 1fr 1.4fr 1.4fr 1fr;
                    }
                    @media all and (max-width: 980px) {
                        grid-template-columns: 1fr 1fr;
                        grid-gap: 2em;
                    }
                    @media all and (max-width: 700px) {
                        grid-template-columns: 1fr;
                    }
                    @media all and (max-width: 480px) {
                        grid-template-columns: 1fr 1fr;
                    }

                    > .menu-item > a {
                        .ff_secondary();
                        .text_ml();
                        font-weight: 400;
                        color: @lavender;
                        position: relative;
                        text-transform: lowercase;
                        display: inline-block;
                        margin-bottom: 1.1em;

                        &:after {
                            content: '';
                            display: inline-block;
                            width: 100%;
                            bottom: -7px;
                            height: 1px;
                            left: 0;
                            position: absolute;
                            background: @lavender;
                        }

                    }
                    .menu-item {

                        &:last-child {
                            grid-column: 4;
                            @media all and (max-width: 980px) {
                                grid-column: initial;
                            }
                            @media all and (max-width: 480px) {
                                grid-column: initial;
                            }
                        }
                        &:nth-child(3) {
                            grid-row: 1 / span 2;
                            grid-column: 3;
                            @media all and (max-width: 980px) {
                                grid-column: initial;
                                grid-row: 1 / span 2;
                            }
                            @media all and (max-width: 480px) {
                                grid-row: 1 / span 2;
                                grid-column: 2;
                            }
                        }
                        .sub-menu {
                            .menu-item {
                                a {
                                    .text_m();
                                    color: #fff;
                                    display: inline-block;
                                    margin-bottom: 0.1em;
                                    transition: .3s;

                                    &:hover {
                                        color: #C9C2F9;
                                        text-decoration: underline;
                                    }
                                }
                            }
                        }
                    }
                }
            }

        }
        .footer_bottom {
            display: grid;
            grid-template-columns: 1fr 1fr;
            justify-content: space-between;
            width: 100%;

            .name_text {
                grid-column: 1 / span 2;
                color: #c9c2f9;
                font-size: 19.773em;
                line-height: 1.4;
                text-transform: uppercase;
                .ff_secondary();
                font-weight: 900;
                margin-left: -0.05em;
                margin-bottom: -0.23em;

                @media all and (max-width: 1380px) {
                    font-size: 17.673em;
                }
                @media all and (max-width: 1100px) {
                    font-size: 15.6em;
                }
                @media all and (max-width: 980px) {
                    font-size: 10.88em;
                }
                @media all and (max-width: 700px) {
                    font-size: 7.48em;
                }
                @media all and (max-width: 480px) {
                    font-size: 7.08em;
                }

            }

            .links_block {
                grid-column: 1 / span 2;
                display: flex;
                justify-content: space-between;
                align-items: center;

                @media all and (max-width: 700px) {
                    flex-direction: column;
                    gap: 1em 2em;
                }
            }

            a.legal_link {
                color: #fff;
                font-weight: 400;
                text-align: left;
                transition: .3s;

                @media all and (max-width: 980px) {
                    grid-column: 1;
                }

                &:hover {
                    color: #C9C2F9;
                    text-decoration: underline;
                }
            }

            .text_s {
                text-align: right;
                @media all and (max-width: 700px) {
                    font-size: 1.2em;
                }
            }
        }

    }
}
