//     : @import '../_source_dev/less/blocks/common_block.less';

.page_title_block.column_2 {
    grid-template-columns: 2fr 1fr;
    
    .podcast_player {
        display: flex;
        justify-content: flex-end;
        
        .controls {
            justify-content: flex-end;
        }
    }
    .subtitle {
        grid-column: 1 / span 2;
    }
    .line_separator {
        grid-column: 1 / span 2;
    }        
}
.page_title_block {
    padding: 0.5em 0 6.4em;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 2.4em;
    align-items: center;
    
    @media all and (max-width: 700px) {
        grid-gap: 3.223em;
    }
    @media all and (max-width: 700px) {
        padding: 1.4174em 0 3.2258em 0;
        
    }
    .line_separator {
        @media all and (max-width: 700px) {
            max-width: none;
        }
    }
    .subtitle_wrapper {
        margin-left: 11em;
        max-width: 560px;
        
        @media all and (max-width: 700px) {
            margin-left: 0;
        }
    }
    &--industry_test,
    &--industry,
    &--oil-gas {
        padding-bottom: 0;
    }
    &--about_us {
        .line_separator {
            max-width: 57.2em;
        }
    }
    &--blog {
        .line_separator {
            max-width: 32.4em;
        }
    }
    &--pricing {
        //padding-bottom: 4.3em;
        .text_4xl {
            line-height: 1;
            max-width: 10em;
            
            span {
                color: @purple;
            }
        }
        .pricing_description {
            .text_md();
            max-width: 28em;
            margin-left: 5.5em;
        }
    }
}



.common_text {
    p {
        .text_m();
        margin-bottom: 1.3em;
        line-height: 1.5;
        
        &:last-child {
            margin-bottom: 0;
        }
    }
    blockquote {
        padding-left: 1.6em;
        border-left: 1px solid;
        margin-left: 1.6em;
        font-style: italic;
    }
    blockquote p {
        margin-bottom: 1.3em !important;
    }
    
    ul {
        li {
            list-style-type: none;
            &:before {
                content: '';
                width: 5px;
                height: 5px;
                display: inline-block;
                position: absolute;
                top: 0.6em;
                left: -1em;
                border-radius: 50%;
                background: @dark;
            }
        }
    }
    
    ul, ol {
        padding-left: 2.3em;
        margin-bottom: 2em;
        
        li {
            margin-bottom: .65em;
            .text_m();
            position: relative;
            line-height: 1.5;
            
            
            li, p {
                font-size: 1em;
            }
            
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    
    p.text_size_s {
        .text_s();
    }
    
    a {
        color: @violet;
        transition: .3s;
        text-decoration: underline;
        
        &:hover {
            color: @lavender;
        }
    }
    
    
    h2 {
        .text_ls();
        font-weight: 600;
        margin-top: .2em;
        margin-bottom: 1em;
        @media all and (max-width: 900px) {
            margin-top: .5em;
            margin-bottom: .5em;
        }
    }
    h3 {
        .text_ls();
        font-weight: 600;
        margin-top: .2em;
        margin-bottom: 1em;
        @media all and (max-width: 900px) {
            margin-top: .5em;
            margin-bottom: .5em;
        }
    }
}


.breadcrumbs {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 3.2em 0;
    
    @media all and (max-width: 700px) {
        padding: 1.5em 0;
    }
    
    .js-last-item {
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }
    
    svg {
        height: 1.6em;
        width: auto;
        fill: none;
        position: relative;
        top: 0.1em;
    }
    .link_main {
        svg {
            width: 1.6em;
        }
    }
    .divider {
        margin: 0 0.6em;
        svg {
            width: 1.2em;
        }
    }
}

@media all and (width: 325px) {
    .line_separator {
        height: 2px;
    }
}


div.wp_table_wrapper {
    overflow-x: auto;
    @media all and (max-width: 980px) {
        width: ~'calc(var(--container_width) - var(--container_padding) - var(--container_padding))' !important;
    }
    @media all and (max-width: 700px) {
        width: ~'calc(100vw - var(--container_padding) - var(--container_padding))' !important;
    }
    table.tablepress {
        @media all and (max-width: 700px) {
            min-width: 640px;
        }
    }
}
table.tablepress {
    font-size: 1.4em;
    border-spacing: 4px;
    border-collapse: separate;
    
    @media all and (max-width: 700px) {
        font-size: 1.6em;
    }
    caption {
        display: none;
    }
    thead {
        tr {
            th {
                background: @lavender;
                color: @dark;
                font-weight: normal;
                text-align: center;
            }
        }
    }
    tbody {
        tr {
            td {
                background: #fff;
                border: none;
            }
        }
    }
}



.result_block {
    
    .section_title {
        margin-bottom: 1.25em;
    }
    .result_block_content {
        grid-template-columns: auto 63em;
        padding-right: 0;
        margin-top: 1.2em;
        display: grid;
        
        @media all and (max-width: 1100px) {
            grid-template-columns: auto;
        }
        
        
        .result_block_list {
            margin-right: 0;
            padding-left: 8em;
            position: relative;
            
            @media all and (max-width: 700px) {
                padding-left: 1.5em;
            }
            
            &:before {
                content: '';
                display: block;
                position: absolute;
                left: 0;
                background: url(../img/oil_gas_vector.png) no-repeat;
                width: 8.1em;
                height: 9%;
                background-size: cover;
                
                @media all and (max-width: 700px) {
                    background: url(../img/industria_vector.png) no-repeat;
                    width: 1.8em;
                    height: 9%;
                }
            }
            &:after {
                z-index: -1;
                content: '';
                display: block;
                position: absolute;
                left: 8em;
                width: .1em;
                background: #252c32;
                height: 97%;
                bottom: 0;
                
                @media all and (max-width: 700px) {
                    left: 1.5em;
                    background: #686B6F;
                    width: .2em;
                }
            }
            
            .result_block_item {
                display: block;
                padding: 2em 3em;
                position: relative;
                
                &:before {
                    content: '';
                    display: block;
                    background: #252c32;
                    width: 1em;
                    height: 2.4em;
                    top: 50%;
                    left: 0;
                    position: absolute;
                    transform: translateY(-50%) translateX(-50%);
                }
            }
        }
        .result_block_img {
            max-width: 63em;
            width: 100%;
            img {
                width: 100%;
                will-change: transform;
                mix-blend-mode: multiply;
            }
        }
    }
}

.reference_block {
    .reference_inner {
        padding-left: 11em;
        max-width: 80em;
        
        @media all and (max-width: 700px) {
            padding-left: 0;
        }
        
        .reference_subtitle {
            margin-bottom: 1.6em;
        }
        .reference_list {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            a {
                margin-bottom: .7em;
                display: inline-block;
                position: relative;
                text-decoration: underline #9588e8;
                text-underline-offset: .24em;
            }
        }
    }
}


.block_btn_link {
    .button_block_demo {
        padding: 0 !important;
        
        .bg_pos {
            left: 50%;
            top: 189%;
            transform: translate(-50%, -50%);
            position: absolute;
        }
        .bgr1,
        .bgr2 {
            width: 110em;
            height: 110em;
        }
        .bgr1 {
            background: url(../img/circles_in_in.png) no-repeat center;
            animation: ratate_industry_demo_revert 50s linear infinite;
            background-size: contain;
        }
        .bgr2 {
            background: url(../img/circles_out_in.png) no-repeat center;
            animation: ratate_industry_demo 50s linear infinite;
            background-size: contain;
        }
        
        
        .inner {
            padding: 6.4em 11em 6.4em 11em !important;
            
            @media all and (max-width: 980px) {
                padding: 8em var(--container_padding) !important;
            }
            @media all and (max-width: 700px) {
                padding: 4em var(--container_padding) !important;
            }
            
            &:before,
            &:after {
                z-index: 11111 !important;
            }
            @media all and (max-width: 700px) {
                &:before {
                    border: 1.5em solid transparent !important;
                    border-left: 1.5em solid #f5f8ff !important;
                    border-top: 1.5em solid #f5f8ff !important;
                }
                &:after {
                    height: 0.3em !important;
                    right: -12.6em !important;
                }
                
            }
        }
        .button_block_demo_content {
            display: flex;
            align-items: center;
            justify-content: space-between;
            
            @media all and (max-width: 700px) {
                display: block;
            }
            
            .btn {
                height: 100%;
                display: block;
            }
            .button_block_demo_content_text {
                min-height: 10em;
                display: flex;
                justify-content: space-between;
                flex-direction: column;
                
                @media all and (max-width: 980px) {
                    min-height: 12em;
                    margin-bottom: 3.3334em;
                }
                @media all and (max-width: 700px) {
                    min-height: 10em;
                }
                @media all and (max-width: 480px) {
                    min-height: 18em;
                }
                .button_block_demo_content_title {
                    text-transform: uppercase;
                    
                    @media all and (max-width: 480px) {
                        font-size: 3.3em !important;
                    }
                    span {
                        color: #C9C2F9;
                    }
                }
                .button_block_demo_content_subtitle {
                    font-weight: 400;
                }
            }
        }
    }
    @keyframes ratate_industry_demo {
        to {
            transform: rotate(0);
        }
        from {
            transform: rotate(380deg);
        }
    }
    @keyframes ratate_industry_demo_revert {
        to {
            transform: rotate(0);
        }
        from {
            transform: rotate(-380deg);
        }
    }
}



.list_image_right_block {
    .container {
        .inner {
            margin-top: 1.2em;
            display: grid;
            grid-template-columns: auto 52em;
            padding-right: 3em;
            
            @media all and (max-width: 1360px) {
                grid-template-columns: auto 46em;
            }
            
            @media all and (max-width: 1100px) {
                padding-right: 0;
                grid-template-columns: auto !important;
            }
        }
        .content_wrapper {
            position: relative;
            margin-right: 11em;
            @media all and (max-width: 1360px) {
                margin-right: 8em;
            }
            @media all and (max-width: 1100px) {
                margin-right: 0 !important;
                order: 2;
            }
            &::before {
                content: '';
                display: block;
                position: absolute;
                left: 0;
                background: url(../img/oil_gas_vector.png) no-repeat;
                width: 8.1em;
                height: 9%;
                background-size: cover;
                @media all and (max-width: 1100px) {
                    display: none;
                }
            }
            &::after {
                z-index: -1;
                content: '';
                display: block;
                position: absolute;
                left: 8em;
                bottom: -2em;
                height: 100%;
                width: .1em;
                background: #252c32;
                @media all and (max-width: 1100px) {
                    display: none;
                }
            }
            
            .content_wrapper_list {
                padding-left: 8em;
                @media all and (max-width: 1100px) {
                    padding-left: 0;
                }
                .content_wrapper_list_item {
                    display: grid;
                    align-items: start;
                    justify-content: space-between;
                    grid-template-columns: 19em 1fr;
                    padding: 2.4em 3em;
                    position: relative;
                    
                    @media all and (max-width: 1100px) {
                        grid-template-columns: 21em 1fr;
                    }
                    @media all and (max-width: 480px) {
                        padding: 0.8em 0;
                    }
                    
                    &:first-child {
                        padding-top: 2em;
                    }
                    &::before {
                        content: '';
                        display: block;
                        background: rgba(37, 44, 50, 1);
                        width: 100%;
                        height: 1px;
                        bottom: 0;
                        left: 0;
                        position: absolute
                    }
                    .item_title {
                        .text_m();
                        font-family: 'Inter Tight', sans-serif;
                        font-weight: 600;
                        color: #252C32;
                    }
                    ul {
                        margin-left: 1em;
                    }
                }
            }
        }
        .industry_content_img {
            padding-top: 2.4em;
            max-width: 520px;
            width: 100%;
            
            @media all and (max-width: 1100px) {
                max-width: none;
                overflow: hidden;
                order: 1;
            }
            @media all and (max-width: 700px) {
                max-height: 35em;
            }
        }
    }
}



.two_column_text_list_block {
    .text_list_content {
        display: grid;
        align-items: start;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 14em;
        justify-content: space-between;
        padding-left: 11em;
        
        @media all and (max-width: 1380px) {
            padding-left: 0;
        }
        @media all and (max-width: 980px) {
            grid-template-columns: auto;
            grid-gap: 0;
        }
        
        .text_list_content_text {
            .section_title {
                margin-left: -2.05555em;
                margin-bottom: .8em;
                
                @media all and (max-width: 1380px) {
                    margin-left: 0;
                }
                @media all and (max-width: 980px) {
                    margin-bottom: 0.371em;
                }
            }
        }
        .text_list_content_list {
            @media all and (max-width: 980px) {
                margin-top: 3.2em;
            }
            @media all and (max-width: 700px) {
                margin-top: 3.3em;
            }
            &--mono {
                ul {
                    li {
                        display: flex;
                        align-items: center;
                        justify-content: left;
                        padding: .7em 1.5em;
                        width: auto;
                        height: auto;
                        background: #776ACA;
                        color: #ffffff;
                        margin-bottom: .9em;

                        @media all and (max-width: 700px) {
                            margin-bottom: 0.51em;
                        }
                        p {
                            
                        }
                    }
                }
            }
            .text_list_content_list_title {
                font-weight: 600;
                color: #252C32;
                margin-bottom: 1.2em;
                
                @media all and (max-width: 700px) {
                    margin-bottom: 0.82em;
                    max-width: 15em;
                }
            }
            div.purple_block {
                .text_block_purple();
            }
        }
    }
}