* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
html {
    overflow-x: hidden;
    width: 100%;
    max-width: 100%;
}
h1 {
    margin: 0;
}
body {
    position: relative;
    font-family: 'Inter Tight', sans-serif;
    font-size: 10px;
    font-weight: 400;
    min-height: 100vh;
    display: flex;
    // contain: paint;
    overflow-x: hidden;
    
    flex-direction: column;
    width: 100%;
    max-width: 100%;
    // transition: .3s;
    color: @black;
    background: @cream;
    // min-height: 100%!important;
    
    @media all and (max-width: 1380px) {
        font-size: 9px;
    }
    @media all and (max-width: 480px) {
        font-size: 1.984vw;
    }
}

body.fix {
    overflow: hidden;
    height: 100vh;
}
main {
    width: 100vw;
    // overflow-x: hidden;
    flex-grow: 1;
    position: relative;
    z-index: 10;
}
.main {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    
    footer {
        margin-top: auto;
    }
}
a, div {
    outline: none !important;
}
a {
    color: inherit;
    text-decoration: none;
}
.container {
    width: var(--container_width);
    padding: 0 var(--container_padding);
    margin: 0 auto;
    
}

