.line_separator {
    width: 100%;
    height: 1px;
    margin-top: ~'calc(0.5em - 0.5px)';
    margin-bottom: ~'calc(0.5em - 0.5px)';
    position: relative;
    background: rgba(37, 44, 50, 1);
    
    &:before,
    &:after {
        content: '';
        display: inline-block;
        width: 3.5em;
        height: 1em;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMzUiIGhlaWdodD0iMTAiIHZpZXdCb3g9IjAgMCAzNSAxMCIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KPHBhdGggZD0iTTExIDBMMCA1TDExIDEwTDM1IDEwVjBMMTEgMFoiIGZpbGw9IiMyNTJDMzIiLz4KPC9zdmc+Cg==");
    }
    &:before {
        left: -1px;
        transform: translateY(-50%) scale(-1, 1);
    }
    &:after {
        right: -1px;
    }
}
.line_separator.left {
    &:after {
        display: none;
    }
}

.scroll_top.visible {
    bottom: 8.1em;
    visibility: visible;
    opacity: 1;
    
    @media all and (max-width: 700px) {
        bottom: 3.2em;
    }
}
.scroll_top {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1em;
    bottom: -3.2em;
    transition: .5s;
    opacity: 0;
    visibility: hidden;
    cursor: pointer;
    left: ~'calc(50% + var(--container_width) / 2 + 3.2em)';
    
    @media all and (max-width: 1640px) {
        left: auto;
        right: 1em;
    }
    .icon_wrapper {
        width: 5.2em;
    }
    p {
        white-space: nowrap;
    }
}


.wow {
    visibility: hidden;
}
.animate_words {
    display: inline-flex;
    flex-wrap: wrap;
}
.anim_item {
    display: inline;
    opacity: 0;
    transition: .3s;
}
.anim_item._show {
    opacity: 1;
}

