.scroll_btn {
    width: 4.6em;
    height: 23.7em;
    position: absolute;
    top: -6em;
    cursor: pointer;
    right: var(--container_padding);
    
    .inner {
        background: #c9c2f9;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 19.1em;
    }
    
    &:before,
    &:after {
        content: '';
        display: inline-block;
        width: 100%;
        background: #c9c2f9;
        left: 0;
        position: absolute;
    }
    &:before {
        bottom: 0;
        height: 1em;
    }
    &:after {
        height: 2.4em;
        bottom: 1.6em;
    }
    
    
    .text_ml {
        position: absolute;
        left: 50%;
        color: @dark;
        bottom: 2.5em;
        transform: translateX(-50%) rotate(-90deg);
        
        @media all and (max-width: 480px) {
            bottom: 1.8em;
        }
    }
    .arrow {
        width: 0.8em;
        height: 5.1em;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 10em;
        background-image: url("data:image/svg+xml;base64,");
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        
        @media all and (max-width: 480px) {
            bottom: 11em;
        }
    }
}
#down_link.scroll_btn {
    bottom: 0;
    top: auto;
    
    .inner {
        top: auto;
        bottom: 0;
    }
    
    .text_ml {
        bottom: 5.8em;
        
        @media all and (max-width: 480px) {
            bottom: 4.5em;
        }
    }
    .arrow {
        transform: translateX(-50%) scale(-1, -1);
        bottom: 3.8em;
        
        @media all and (max-width: 480px) {
            bottom: 3.5em;
        }
    }
    
    &:before {
        bottom: auto;
        top: 0;
    }
    &:after {
        bottom: auto;
        top: 1.6em;
    }
}

.current-menu-item > a {
