.text_block_purple {
  padding: 1.5em 2em;
  background: #776ACA;
  width: 100%;
  height: auto;
  font-weight: 400;
  color: #FFFFFF;
  position: relative;
  &:before {
    content: '';
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    border: .5em solid transparent;
    border-left: .5em solid #f5f8ff;
    border-top: .5em solid #f5f8ff;
  }
  &:after {
    content: '';
    display: inline-block;
    position: absolute;
    bottom: 0;
    right: 0;
    border: .5em solid transparent;
    border-right: .5em solid #f5f8ff;
    border-bottom: .5em solid #f5f8ff;
  }
}