//     : @import './src/less/blocks/about.less';



.about {
  padding: 12.5em 0 5em 0;
  
  @media all and (max-width: 980px) {
    padding-top: 11.4em;
    padding-bottom: 1.1em;
  }
  @media all and (max-width: 700px) {
    padding-top: 6em;
    padding-bottom: 6em;
  }
  @media all and (max-width: 480px) {
    padding-top: 11em;
  }
  
  .container {
    .text_block {
      display: grid;
      grid-template-columns: 1fr;
      grid-gap: 2.7em 0;
      
      .title_box {
        
      }
      
      .text_box {
        padding-left: 11em;
        max-width: 65em;
        padding-bottom: 1.4em;

        @media all and (max-width: 980px) {
          padding-left: 12em;
          padding-bottom: 1.9em;
        }

        @media all and (max-width: 700px) {
          padding-left: 0;
        }
        @media all and (max-width: 480px) {
          padding-bottom: 3em;
        }
      }
      
      .about_cards_list {
        display: grid;
        grid-template-columns: repeat(3, 29.8em);
        justify-content: flex-end;
        padding-top: 1.3em;
        grid-gap: 3.3em;

        @media all and (max-width: 1100px) {
          grid-gap: 2em;
          grid-template-columns: repeat(3, 28em);
        }
        @media all and (max-width: 980px) {
          grid-template-columns: repeat(3, 1fr);
        }
        @media all and (max-width: 700px) {
          grid-template-columns: 1fr;
        }
        
        .about_card_item {
          @media all and (max-width: 700px) {
            display: grid;
            grid-template-columns: 10em 1fr;
            grid-gap: 2em;
            align-items: center;
          }
          @media all and (max-width: 480px) {
            grid-template-columns: 13.3em 1fr;
          }
          
          .icon_wrapper {
            width: 10em;
            margin-bottom: 2.1em;

            @media all and (max-width: 980px) {
              width: 11.2em;
              margin-bottom: 2.9em;
            }
            @media all and (max-width: 700px) {
              width: 10em;
              margin-bottom: 0;
            }

            @media all and (max-width: 480px) {
              width: 13.3em;
            }
          }
        }
      }
    }
  }
}