.industry_section {
  margin-bottom: 7.2em;
  @media all and (max-width: 980px) {
    margin-bottom: 8.9em;
  }
  @media all and (max-width: 700px) {
    margin-bottom: 4.33em;
  }
}
.industry_section:last-child {
  margin-bottom: 0;
}
.industry {
  margin-bottom: 6.4em;
  .result_title {
    margin-bottom: 1.25em;
  }
  .page_title_block {
    padding: 0.5em 0 2.4em;
  }
  &_title-h1 {
    span {
      color: rgba(149, 136, 232, 1);
    }
  }
  &_subtitle {
    max-width: 560px;
    width: 100%;
    font-weight: 400;
    color: #252C32;
    margin-left: 5.5em;
  }
  &_content {
    margin-top: 1.2em;
    display: grid;
    grid-template-columns: auto 52em;
    padding-right: 3em;
    &_wrapper{
      position: relative;
      margin-right: 11em;
      &::before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        background: url(../img/oil_gas_vector.png) no-repeat;
        width: 8.1em;
        height: 9%;
        background-size: cover;
      }
      &::after {
        z-index: -1;
        content: '';
        display: block;
        position: absolute;
        left: 8em;
        bottom: -2em;
        height: 100%;
        width: .1em;
        background: #252c32;
      }
      &_list {
        padding-left: 8em;
        &_item {
          display: grid;
          align-items: start;
          justify-content: space-between;
          grid-template-columns: 19em 1fr;
          padding: 2.4em 3em;
          position: relative;
          &:first-child {
            padding-top: 2em;
          }
          &::before {
            content: '';
            display: block;
            background: rgba(37, 44, 50, 1);
            width: 100%;
            height: 1px;
            bottom: 0;
            left: 0;
            position: absolute
          }
          .item_title {
            .text_m();
            font-family: 'Inter Tight', sans-serif;
            font-weight: 600;
            color: #252C32;
          }
          ul {
            margin-left: 1em;
          }
        }
      }
    }
    &_img {
      padding-top: 2.4em;
      max-width: 520px;
      width: 100%;
    }
  }
  &_customer_content {
    display: grid;
    align-items: start;
    grid-template-columns: repeat(2, 1fr);
    gap: 14em;
    justify-content: space-between;
    padding-left: 11em;
    &_text {
      h2 {
        margin-left: -2.05555em;
        margin-bottom: .8em;
      }
      p {
        font-weight: 400;
        color: #252C32;
        margin-bottom: 1.5em;
        span, a {
          color: rgba(149, 136, 232, 1);
        }
      }
      p:last-child {
        margin-bottom: 0;
      }
    }
    &_list {
      &_title {
        font-weight: 600;
        color: #252C32;
        margin-bottom: 1.2em;
      }
      &_item {
        display: flex;
        align-items: center;
        justify-content: left;
        padding: .7em 1.5em;
        width: auto;
        height: auto;
        background: #776ACA;
        color: rgba(255, 255, 255, 1);
        margin-bottom: .9em;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
  &_encryption {
    width: 100%;
    &_title {
      display: flex;
      align-items: center;
      justify-content: center;
      h2 {
        font-family: 'Orbitron', sans-serif;
        font-weight: 600;
        //font-size: 2.8em;
        line-height: 140%;
        color: #252C32;
        margin-bottom: 1.44em;
        span {
          color: #9588e8;
        }
      }
    }
    &_content {
      display: grid;
      justify-content: space-between;
      grid-auto-columns: auto;
      // grid-template-columns: repeat(auto-fit, minmax(15em, 1fr));
      grid-template-columns: max-content 1fr 1fr 1fr 1fr 1fr;
      align-items: start;
      &_item {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;
        &:last-child {
          position: relative;
          .industry_encryption_content_item_img:before {
            content: none;
          }
        }
        &_img {
          display: grid;
          align-items: start;
          justify-content: space-between;
          grid-template-columns: 1fr auto;
          width: 100%;
          div {
            position: relative;
            max-width: 79px;
            width: 100%;
            margin: 0 auto;
            img {
              will-change: transform;
            }
          }
          &:before {
            content: '';
            display: inline-block;
            background: url(../img/arrow_encript.png) no-repeat;
            background-size: contain;
            width: 3em;
            transform: rotate(270deg);
            height: 3em;
            order: 2;
          }
        }
        &_title {
          max-width: 14.2em;
          font-family: 'Inter Tight', sans-serif;
          font-weight: 400;
          color: #252C32;
        }
      }
    }
  }
  &_solution_content {
    margin-top: 3.2em;
    margin-bottom: 7.2em;
    &_text_mt {
      margin-top: 3.2em;
    }
    &_text {
      max-width: 40em;
      width: 100%;
      padding-left: 6.9em;
      p {
        font-weight: 400;
        color: #252C32;
        margin-bottom: 1.5em;
        span, a {
          color: rgba(149, 136, 232, 1);
        }
      }
      p:last-child {
        margin-bottom: 0;
      }
      div {
        padding: 0.688em 1.5em;
        gap: 1.6em;
        background: #776ACA;
        font-weight: 600;
        color: #FFFFFF;
        margin-bottom: 1em;
      }
    }
    &_wrapper {
      display: grid;
      grid-template-columns: auto 52em;
      &_img {
        max-width: 52em;
        width: 100%;
      }
    }
    &_list {
      margin-top: 3.2em;
      &_grid {
        display: grid;
        grid-template-columns: repeat(3, auto);
        align-items: end;
        justify-content: space-between;
        &_item {
          max-width: 41em;
          width: 100%;
        }
      }
      &_carusel {
        display: none;
        width: 100%;
        &_item {
          // max-width: 41em;
          width: 100%;
        }
      }
      
    }
    &_detailed_description {
      &_text {
        padding-left: 7em;
        max-width: 40em;
        width: 100%;
        .text_m();
        margin-bottom: 2em;
        font-weight: 400;
      }
      &_table {
        padding: 0 11em;
        table {
          th, td {
            border: 1px solid #f5f8ff;
          }
          th {
            background-color: #C9C2F9;
            width: 13.2em;
            padding-top: 1em;
            padding-bottom: 1em;
            font-weight: 400;
            text-align: center;
          }
          span {
            .text_m();
            line-height: normal;
          }
          td {
            background-color: #FFFFFF;
            span {
              .text_m();
              line-height: normal;
              display: flex;
              align-items: center;
              justify-content: center;
              padding-top: .5em;
              padding-bottom: .5em;
            }
          }
        }
      }
      &_additional_information {
        display: grid;
        align-items: start;
        justify-content: space-between;
        padding: 0 7em;
        grid-template-columns: repeat(2, 1fr);
        .text_m();
        font-weight: 400;
        gap: 2.15em;
        .additional_information_left {
          
        }
        .additional_information_right {
          .industry_block_purple {
            background: url(../img/path.png) no-repeat right #776ACA;
            background-size: contain;
          }
        }
      }
      &_list {
        display: grid;
        grid-template-columns: repeat(2, auto);
        gap: 11em;
        &_item {
          .item_img {
            max-width: 59em;
            width: 100%;
          }
          .item_text {
            margin-top: .72em;
            text-align: center;
            .text_s();
            font-weight: 600;
          }
        }
      }
    }
  }
  &_content_results {
    grid-template-columns: auto 63em;
    padding-right: 0;
    .industry_content_wrapper {
      margin-right: 0;
    }
    .industry_content_img {
      max-width: 63em;
      width: 100%;
      img {
        width: 100%;
        will-change: transform;
        mix-blend-mode: multiply;
      }
    }
    .industry_content_wrapper::after {
      bottom: 0;
      height: 97%;
    }
    .industry_content_wrapper_list_item::before {
      content: '';
      display: block;
      background: #252c32;
      width: 1em;
      height: 2.4em;
      top: 50%;
      left: 0;
      position: absolute;
      transform: translateY(-50%) translateX(-50%);
    }
    .industry_content_wrapper_list_item {
      display: block;
      padding: 2em 3em;
      position: relative;
    }
    .industry_content_wrapper_list_item:first-child {
      padding-top: 2em;
    }
  }
  &_results_link {
    padding-left: 11em;
    max-width: 80em;
    &_title {
      margin-bottom: 1.6em;
      font-weight: 600;
      .text_m();
    }
    &_text {
      .text_m();
      a {
        margin-bottom: .7em;
        display: inline-block;
        position: relative;
        text-decoration: underline #9588e8;
        text-underline-offset: .24em;
      }
    }
  }
  &_results_demo {
    padding: 0 !important;
    .bg_pos {
      left: 50%;
      top: 189%;
      transform: translate(-50% , -50%);
      position: absolute;
    }
    .bgr1, .bgr2 {
      width: 110em;
      height: 110em;
    }
    .bgr1 {
      background: url(../img/circles_in_in.png) no-repeat center;
      animation: ratate_industry_demo_revert 50s linear infinite;
      background-size: contain;
    }
    .bgr2 {
      background: url(../img/circles_out_in.png) no-repeat center;
      animation:ratate_industry_demo 50s linear infinite;
      background-size: contain;
    }
    .inner {
      padding: 6.4em 11em 6.4em 11em !important;
      &::before, &::after {
        z-index: 11111 !important;
      }
    }
    &_content {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .btn {
        height: 100%;
        display: block;
      }

      &_text {
        height: 10em;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
      }
      &_title {
        text-transform: uppercase;
        span {
          color: #C9C2F9;
        }
      }
      &_subtitle {
        font-weight: 400;
      }
    }
  }
  
}
@keyframes ratate_industry_demo {
  to {
    transform: rotate(0);
  }
  from {
    transform: rotate(380deg);

  }
}
@keyframes ratate_industry_demo_revert {
  to {
    transform: rotate(0);
  }
  from {
    transform: rotate(-380deg);

  }
}
.slider-pagination {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  margin-top: 10px;
  font-family: 'Orbitron';
  font-weight: 400;
  .text_m();
  .current-slide {
    color: @purple;
  }
  .slider-pagination-prev, .slider-pagination-next {
    cursor: pointer;
  }
}
.industry_challenge_content_list_title {
  font-weight: normal;
  .text_m();

}
.industry_challenge_content {
  align-items: end;
}
.industry_block_purple {
  padding: 1.5em 2em;
  background: #776ACA;
  width: 100%;
  height: auto;
  font-weight: 400;
  color: #FFFFFF;
  position: relative;
  &:before {
    content: '';
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    border: .5em solid transparent;
    border-left: .5em solid #f5f8ff;
    border-top: .5em solid #f5f8ff;
  }
  &:after {
    content: '';
    display: inline-block;
    position: absolute;
    bottom: 0;
    right: 0;
    border: .5em solid transparent;
    border-right: .5em solid #f5f8ff;
    border-bottom: .5em solid #f5f8ff;
  }
}

@media all and (max-width: 1380px) {
  .industry_solution_content_detailed_description_additional_information {
    padding: 0;
  }
  .industry_solution_content_detailed_description_text {
    padding-left: 0;
    max-width: 34em;
  }
  .industry_solution_content_detailed_description_table {
    padding: 0;
    table {
      width: 100%;
    }
  }
  .industry_solution_content_text {
    padding-left: 0;
    padding-right: 8em;
  }
  .industry_customer_content {
    padding-left: 0;
    h2 {
      margin-left: 0;
    }
  }
}
@media all and (max-width: 1100px) {
  .industry_solution_content_list_grid {
    grid-template-columns: repeat(2, auto);
  }
  .industry_solution_content_text {
    padding-right: 0;
    max-width: none;
  }
  .industry_solution_content_wrapper {
    grid-template-columns: auto;
    &_img {
      margin-top: 3em;
    }
  }
  .industry_solution_content_text {
    margin-top: 2.4em;
  }
  .industry_content_results { 
    place-items: center;
    .industry_content_img {
      max-width: 50em;
      width: 100%;
    }
    .industry_content_wrapper {
      order: 0;
      &_list {
        padding-left: 8em;
      }
      &:before, &::after {
        content: '';
      }
    }
  }
  .industry_results_content_wrapper {
    grid-template-columns: auto;
    justify-content: normal;
    &_list {
      margin-top: 3.4em;
      &:before {
        top: -3em;
        left: -10.9em;
      }
    }
  }


  .industry_encryption_content {
    grid-template-columns: repeat(auto-fit, minmax(20em, 1fr));
  }

  .industry_content {
    grid-template-columns: auto;
    padding-right: 0;
  }
  .industry_content_wrapper {
    order: 2;
    margin-right: 0;
    &:before, &::after {
      content: none;
    }
    &_list {
      padding-left: 0;
    }
  }
  .industry_content_wrapper_list_item {
    grid-template-columns: 21em 1fr;
  }
  .industry_content_img {
    max-width: none;
    overflow: hidden;
    max-height: 50em;
  }
}
@media all and (max-width: 980px) {
  .industry_results_link {
    padding-left: 0;
  }
  .industry_results_demo_content {
    display: block;
    &_text {
      height: 12em;
      margin-bottom: 3.3334em;
    }
    .btn {
      width: 25em !important;
    }
  }
  .industry_results_demo .inner {
    padding: 8em var(--container_padding) !important;
  }
  .industry_solution_content_detailed_description_list {
    grid-template-columns: auto;
    gap: 5em;
    justify-content: center;
  }
  .industry_solution_content_detailed_description_additional_information {
    grid-template-columns: auto;
  }
  .industry_solution_content_detailed_description_table {
    table {
      tbody {
        display: grid;
        grid-template-columns: auto auto auto;
        tr {
          display: flex;
          flex-direction: column;
          align-items: unset;
          justify-content: center;
          td, th {
            width: auto;
            height: 100%;
            min-height: 6em;
            display: flex;
            align-items: center;
            justify-content: center;
          }
        }
      }
    }
  }
  .industry_solution_content_list_carusel {
    display: block;
  }
  .industry_solution_content_list_grid {
    display: none;
    opacity: 0;
    pointer-events: none;
  }
  .industry_solution_content_text {
    padding-left: 0;
    max-width: none;
  }
  .industry_solution_content {
    margin: 4.5em 0;
  }
  .industry_results_content_wrapper_list div:before {
    left: -2.45em;
  }
  .industry_results_content_wrapper_list:before {
    top: -3.3em;
    left: -10.9em;
  }
  .industry_encryption_content {
    grid-template-columns: repeat(auto-fit, minmax(15em, 1fr));
  }

  .industry_separator {
    margin-left: 0;
  }
  .industry_subtitle {
    margin-left: 0;
  }
  .industry_customer_content {
    grid-template-columns: auto;
    gap: 0;
    &_text {
      h2 {
        margin-bottom: .371em;
      }
    }
    &_list {
      margin-top: 3.2em;
    }
  }
}
@media all and (max-width: 700px) {
  .industry_results_demo {
    .inner::before {
      border: 1.5em solid transparent !important;
      border-left: 1.5em solid #f5f8ff !important;
      border-top: 1.5em solid #f5f8ff !important;
    }
    .inner::after {
      height: 0.3em !important;
      right: -12.6em !important;
    }
    .inner {
      padding: 4em var(--container_padding) !important;
    }
  }
  .industry_results_demo_content {
    display: block;
    &_text {
      height: 18em;
    }
  }
  .industry_content_results {
    .industry_content_wrapper::before {
      background: url(../img/industria_vector.png) no-repeat;
      width: 1.8em;
      height: 9%;
    }
    .industry_content_wrapper_list {
      padding-left: 1.5em;
    }
    .industry_content_wrapper::after {
      left: 1.5em;
      background: #686B6F;
      width: .2em;
    }
    .industry_content_img {
      max-width: 39em;
      overflow: visible;
    }
  } 
  .industry_solution_content {
    margin-top: 1.1em;
    margin-bottom: 0;
  }
  .industry_solution_content_text p, .industry_solution_content_text div {
    margin-bottom: 1.1em;
  }
  .industry_results_content_wrapper_img {
    max-width: 39.1em;
    width: auto;
    margin-left: -5%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 3.24em;
  }
  .industry_results_content {
    padding-left: 6em;
  }
  .industry_results_content_wrapper_list:before {
    top: -1.5em;
    left: -5.3em;
    background: url(../img/lineoil.png) no-repeat !important;
  }
  .industry_results_content_wrapper_list:after {
    width: 2px;
  }
  .industry_results_content_wrapper_list div {
    margin-bottom: 1.52em;
  }
  .industry_encryption_content_item:last-child:before {
    width: 9em;
    height: 1em;
    left: auto;
    right: -1em;
    bottom: auto;
    top: -3.5em;
    scale: 2;
    transform: rotate(270deg);
  }
  .industry_encryption_content_item:last-child:after {
    width: 12em;
    height: 1em;
    font-size: 1.6em;
    left: auto;
    right: -5em;
    top: -50%;
    transform: translateY(-50%) rotate(-90deg);
    bottom: auto;
  }
  .industry_encryption_content {
    display: block;
    &_item {
      align-items: start;
      display: grid;
      grid-template-columns: repeat(2, auto);
      justify-content: start;
      &_img {
        align-items: center;
        justify-content: start;
        grid-template-columns: auto;
        grid-template-rows: auto auto;
        width: auto;
        &:before {
          transform: rotate(0);
          margin: 1.5em 0 1.5em 6em;
          width: 100%;
          height: 4em;
        }
      }
      &_title {
        max-width: 13em;
        margin-left: 1.4em;
        text-align: left;
        span {
          display: inline;
        }
      }
    }
  }




  .industry_separator {
    margin-bottom: 0;
  }
  .industry_content_img {
    max-height: 35em;
  }
  .industry_customer_content_list {
    margin-top: 3.3em;
    &_title {
      margin-bottom: .82em;
      max-width: 15em;
    }
    &_item {
      margin-bottom: .51em;
    }
  }
  .industry_customer_content_text p {
    margin-bottom: 1.1em;
  }
}

@media all and (max-width: 480px) {
  .industry_results_demo_content {
    display: block;
    &_text {
      height: 18em;
    }
    &_title {
      font-size: 3.3em !important;
    }
    .btn {
      width: auto !important;
    }
  }
  .industry_results_content_wrapper_list div:before {
    left: -2.12em;
  }
  .industry_content_wrapper_list_item {
    padding: .8em 0;
  }
}
@media all and (max-width: 444px) {
  .industry_results_content_wrapper_list::before {
    left: -5.4em;
  }
}
@media all and (max-width: 375px) {
  .industry_results_content_wrapper_list::before {
    left: -5.7em;
    width: 3em;
  }
}
@media all and (max-width: 320px) {
  .industry_results_content_wrapper_list::before {
    left: -6em;
    width: 3em;
  }
}