.promo {
    padding: 2.4em 0 0 0;
    height: 90em;
    position: relative;
    background: #776aca;
    
    @media all and (max-width: 980px) {
        height: 87em;
    }
    
    @media all and (max-width: 700px) {
        // padding: 0;
        // height: 70em;
    }
    @media all and (max-width: 480px) {
        height: 100vh;
        max-height: 680px;
    }
    .container {
        height: 100%;
    }
    
    .bgr_desktop,
    .bgr_mobile {
        display: inline-block;
        width: 192em;
        height: 90em;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        overflow: hidden;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        z-index: 1;
        
        .dot {
            position: absolute;
            width: 0.4em;
            height: 0.4em;
            background: #fff;
            box-shadow: 0px 0px 6px 2px #C9C2F9;
            border-radius: 50%;
            transform: translate(-50%, -50%);
        }
        .dot_1 {
            animation: dot_1 3s linear infinite;
        }
        .dot_2 {
            animation: dot_2 3s linear infinite;
        }
        
        @keyframes dot_1 {
            0% { top: 28.1%; left: 100%; opacity: 1; }
            50% { top: 28.1%; left: 69.6%; opacity: 1; }
            60% { top: 25%; left: 68.1%; opacity: 1; }
            100% { top: 25%; left: 50%; opacity: 0; }
        }
        @keyframes dot_2 {
            0% { top: 75.9%; left: 0%; opacity: 1; }
            20% { top: 75.9%; left: 7.2%; opacity: 1; }
            30% { top: 78.1%; left: 8%; opacity: 1; }
            100% { top: 78.1%; left: 58%; opacity: 0; }
        }
        
        @media all and (max-width: 480px) {
            border-radius: 0;
            font-size: 7.55px;
        }
        
        p {
            position: absolute;
            bottom: -0.2em;
            right: -.05em;
            opacity: 0.2;
            color: #9588e8;
            .ff_secondary();
            font-weight: 400;
            text-transform: uppercase;
            font-size: 10.4em;
            
            @media all and (max-width: 1380px) {
                font-size: 8.1em;
            }
            @media all and (max-width: 980px) {
                font-size: 5.1em;
                bottom: 0.4em;
                right: auto;
                left: 29%;
            }
            @media all and (max-width: 700px) {
                display: none;
            }
        }
    }
    // .bgr_mobile {
    //     display: none;
    //     @media all and (max-width: 700px) {
    //         display: inline-block;
    //     }
    // }
    .bgr_desktop {
        display: inline-block;
        // @media all and (max-width: 700px) {
        //     display: none;
        // }
    }
    
    .container {
        position: relative;
        z-index: 10;
    }
    
    .promo_inner {
        position: relative;
        padding: 2.8em 0 3.3em;
        min-height: 74.8em;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        
        @media all and (max-width: 700px) {
            min-height: 67.8em;
        }
        @media all and (max-width: 480px) {
            padding-bottom: 17.3em;
            min-height: initial;
            height: 100vh;
            max-height: 680px;
        }
        
        .promo_text {
            display: grid;
            grid-template-columns: 1.05fr 1fr;
            padding-bottom: 6.8em;
            grid-gap: 8em 0;
            margin-top: auto;
            
            @media all and (max-width: 980px) {
                grid-template-columns: 1fr;
                grid-gap: 5.3em;
                // margin-top: 3.5em;
            }
            @media all and (max-width: 700px) {
                padding-bottom: 10em;
            }
            @media all and (max-width: 480px) {
                grid-gap: 6.5em;
            }
            
            
            .title {
                grid-column: 1 / span 2;
                grid-row: 1 / span 1;
                padding-right: 1em;
                position: relative;
                color: @white;
                font-size: 12.5em;
                line-height: 1.2;
                font-weight: 600;
                text-transform: uppercase;
                letter-spacing: -.02em;
                
                @media all and (max-width: 1100px) {
                    font-size: 9.5em;
                }
                
                @media all and (max-width: 980px) {
                    grid-row: initial;
                    grid-column: initial;
                    font-size: 8.2em;
                }
                @media all and (max-width: 700px) {
                    font-size: 6.4em;
                    padding: 0;
                }
            }
            .text_ml {
                grid-row: 1;
                grid-column: 2;
                margin-top: auto;
                max-width: 26em;
                color: @white;
                position: relative;
                position: relative;
                top: -1.4em;
                @media all and (max-width: 1100px) {
                    top: -0.8em;
                }
                @media all and (max-width: 980px) {
                    grid-row: initial;
                    grid-column: initial;
                    margin-left: 5.5em;
                }
                @media all and (max-width: 700px) {
                    max-width: 19em;
                }
                @media all and (max-width: 480px) {
                    margin-left: 0;
                    max-width: initial;
                    width: 100%;
                }
            }
        }
    }
}
