.faq {
  margin-bottom: 6.4em;
  &_content {
    display: grid;
    grid-template-columns: 42.3em 74em;
    gap: 9em;
    justify-content: space-between;
    &_feedback {
      position: relative;

      &:before, &:after {
        content: '';
        display: block;
        position: absolute;
        width: 8.1em;
        height: 100%;
        right: -2.8em;
      }
      &:after {
        background: #5F656B;
        width: .2em;
        top: 0;
      }
      &:before {
        top: -5.5em;
        background: url(../img/faq_line.svg) no-repeat;
        background-size: auto;
      }
      .feedback_fixed {
        max-width: 42.3em;
        width: 100%;
        position: sticky;
        top: 4em;

        &_title {
          margin-top: 0.6em;
        }

        &_text {
          margin-top: 1.5em;
          line-height: 1.5em;
          font-family: 'Inter Tight', sans-serif;
          font-style: normal;
          font-weight: 400;
          span {
            color: rgba(149, 136, 232, 1);
          }
        }
        &_img {
          max-width: 423px;
          width: 100%;
          img {
            width: 100%;
            will-change: transform;
          }
        }
      }
    }
    &_questions {
      display: flex;
      align-items: start;
      justify-content: space-between;
      flex-direction: column;
      height: 100%;
      &_element {
        position: relative;
        width: 100%;
        height: auto;
        &:before {
          content: '';
          display: inline-block;
          width: 1em;
          height: 2.4em;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: -10.5em;
          z-index: 2;
          background: #252C32;
          transition: .3s;
        }
      }
      &_item {
        position: relative;
        padding: 1.6em 3.2em;
        max-width: 740px;
        width: 100%;
        background: #FFFFFF;
        margin-bottom: 1.6em;
        &:before, &:after {
          content: '';
          display: inline-block;
          position: absolute;

        }
        &:before {
          top: 0;
          left: 0;
          border: 1em solid transparent;
          border-left: 1em solid #f5f8ff;
          border-top: 1em solid #f5f8ff;
        }
        &:after {
          bottom: 0;
          right: 0;
          border: 1em solid transparent;
          border-right: 1em solid #f5f8ff;
          border-bottom: 1em solid #f5f8ff;
        }
        &_top {
          display: grid;
          grid-template-columns: 7.5em auto;
          align-items: center;
          cursor: pointer;
          &_icon {
            width: 4.1em;
            height: 4.1em;
            border-radius: 100%;
            background: #F5F5F5;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 3em;
            transition: .2s linear;
            svg {
              width: 1.5em;
              height: 1.5em;
            }
          }
          &_question {
            .text_ml();
            transition: .2s linear;
          }
        }
        &_answer {
          .text_m();
          padding-left: 4.7em;
          opacity: 0;
          height: 0;
          margin: 0;
          --mt: 1em;
          margin-bottom: 0.3em;
        }
      }
    }
  }
}
.question-active {
  // font-weight: 600;
  // font-size: 3em;
  color: #776aca;
}
.answer_false {
  animation: close_answer .2s linear forwards;
}
.answer_true {
  animation: open_answer .2s linear forwards;
}
@keyframes open_answer {
  0% {
    height: 0;
    opacity: 0;
  }
  50% {
    opacity: 0;
    height: 1em;
  }
  100% {
    margin-top: .75em;
    opacity: 1;
    height: auto;
  }
}
@keyframes close_answer {
  0% {
    height: auto;
    opacity: 0;
    margin-top: .75em;
  }
  50% {
    opacity: 0;
    margin-top: .75em;
    height: 1em;
  }
  100% {
    opacity: 0;
    height: 0;
    margin-top: 0;
    display: none;
  }
}

@media all and (max-width: 1380px) {
  .faq_content_feedback:before, .faq_content_feedback:after, .faq_content_questions_element:before {
    content: none;
  }
  .faq_content {
    grid-template-columns: 42.3em auto
  }
}

@media all and (max-width: 980px) {
  .faq_content_questions_item_top {
    cursor: default;
  }
  .faq_content {
    grid-template-columns: auto;
    place-items: start;
  }
  .faq_content_feedback {
    //margin-top: 8.9em;
    order: 2;
  }
}
@media all and (max-width: 700px) {
  .faq_content_questions_item_top {
    grid-template-columns: 1fr 7.5em;

    .faq_content_questions_item_top_icon {
      grid-row: 1;
      grid-column: 2;
      margin-right: 0;
      margin-left: auto;
    }
    .faq_content_questions_item_top_question {
      grid-row: 1;
      grid-column: 1;
    }
  }
  .faq_content_questions_item_top_icon {
    margin-right: 3.3em;
  }
  .faq_content_questions_item_answer {
    --mt: .8em;
    padding-left: 0;
  }
  .faq_content_questions_item_top_question {
    font-size: 1.8em;
  }
  .question-active {
    font-size: 2.3em;
  }
  .faq_content_questions_item {
    margin-bottom: 2.16em;
  }
  .faq_content_questions_element:last-child {
    .faq_content_questions_item {
      margin-bottom: 1.16em;
    }
  }
  .faq_content {
    gap: 0;
  }
  .faq_content_feedback .feedback_fixed_title {
    //margin-top: 0.326em;
    margin-top: 0;
    font-size: 4em;
  }
  .faq_content_feedback .feedback_fixed_text {
    margin-top: 1.01em;
  }
  .faq_content_feedback .feedback_fixed {
    display: flex;
    flex-direction: column;
    max-width: none;
    align-items: center;
  }
  .faq_content_feedback .feedback_fixed_img {
    order: -1;
    max-width: 21em;
  }
  .faq {
    margin-bottom: 4.332em;
  }
}
@media all and (max-width: 480px){
  .faq_content_questions {
    margin: 0 -3em;
  }
}
