//     : @import './src/less/blocks/legal.less';


.legal {
    padding: 4em 0;

    .legal_links {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        gap: 2em;
        padding-bottom: 3em;

        a {
            .text_ml();            
        }
        a.active {
            .color_violet();
        }
    }

    ol > li li { font-size: 1em ! important; } 
    ol { list-style: none; padding-top: 0.6em; } 
    ol li::before { counter-increment: li; content: counters(li, ".") ". ";} 
    ol:first-of-type { counter-reset: li; }
    ol > li > p { margin: 0 ! important;} 
    > .container > ol > li:before { font-weight: bold; }
    
    h1 {
        .ff_secondary();
        .text_l();
        margin-bottom: 0.5em;
    }
    
    h2 {
        .ff_secondary();
        .text_ls();
        margin-bottom: 0.5em;
    }
    
    h3 {
        .ff_secondary();
        .text_ml();
        margin-bottom: 0.5em;
    }
    
    p {
        margin-bottom: 1em;
        .text_m();
    }
    
    ul, ol {
        padding-left: 1.8em;
        li {
            margin-bottom: 1em;
            .text_m();
        }
    }

    .container > a {
        margin-bottom: 1em;
        .text_m();
    }
    
    a {
        .color_violet();
    }
}
