.oil_section {
  margin-bottom: 7.2em;
  @media all and (max-width: 980px) {
    margin-bottom: 8.9em;
  }
  @media all and (max-width: 700px) {
    margin-bottom: 4.33em;
  }
}
.oil_section:last-child {
  margin-bottom: 0;
}
.Oil_Gas {
  margin-bottom: 6.4em;
  .result_title {
    margin-bottom: 1.25em;
  }
  .page_title_block {
    padding: 0.5em 0 2.4em;
  }
  &_title-h1 {
    span {
      color: rgba(149, 136, 232, 1);
    }
  }
  &_subtitle {
    //margin-top: 2em;
    max-width: 560px;
    width: 100%;
    font-weight: 400;
    color: #252C32;
    margin-left: 5.5em;
  }
  &_separator {
    //margin-top: 4.6em;
    margin-bottom: 1.2em;
  }
  &_content {
    display: grid;
    grid-template-columns: repeat(2, auto);
    align-items: start;
    justify-content: space-between;
    gap: 2em;
    margin-top: 3.5em;
    &_info {
      width: 100%;
      position: relative;
      //padding-left: 8em;
      height: 100%;
      &:before {
        content: '';
        position: absolute;
        display: block;
        background: url(../img/oil_gas_vector.png) no-repeat;
        background-size: contain;
        width: 8em;
        height: 100%;
        left: .1em;
        z-index: 2;
        top: -2em;
      }
      //&:after {
      //  z-index: -1;
      //  content: '';
      //  display: block;
      //  left: calc(~'78px - 1%');
      //  bottom: 0;
      //  height: 90%;
      //  width: .1em;
      //  background: rgba(37, 44, 50, 1);
      //  position: absolute;
      //}
      &_wrapper {
        padding-left: 7.1em;
        &_item {
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          align-items: center;
          font-family: 'Inter Tight', sans-serif;
          border-bottom: 1px solid;
          padding: 3em 2.9em;
          border-image-source: linear-gradient(0deg, #252C32, #252C32), linear-gradient(0deg, rgba(37, 44, 50, 0.2), rgba(37, 44, 50, 0.2));
          &:first-child {
            padding-top: 0;
          }
          &:last-child {
            border-bottom: none;
          }
          &_title {
            font-weight: 600;
            color: rgba(37, 44, 50, 1);
          }
          &_text {
            font-weight: 400;
            color: rgba(37, 44, 50, 1);
          }
        }
      }
    }
    &_img {
      max-width: 520px;
      width: 100%;
      img {
        width: 100%;
        will-change: transform;
      }
    }
  }
  &_customer_content {
    display: grid;
    align-items: start;
    grid-template-columns: repeat(2, 1fr);
    gap: 14em;
    justify-content: space-between;
    padding-left: 11em;
    &_text {
      h2 {
        margin-left: -2.05555em;
        margin-bottom: .8em;
      }
      p {
        font-weight: 400;
        color: #252C32;
        margin-bottom: 1.5em;
        span, a {
          color: rgba(149, 136, 232, 1);
        }
      }
      p:last-child {
        margin-bottom: 0;
      }
    }
    &_list {
      &_title {
        font-weight: 600;
        color: #252C32;
        margin-bottom: 1.2em;
      }
      ul {
        list-style: none;
      }
      &_item {
        display: flex;
        align-items: center;
        justify-content: left;
        padding: .7em 1.5em;
        width: auto;
        height: auto;
        background: #776ACA;
        color: rgba(255, 255, 255, 1);
        margin-bottom: .9em;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
  &_challenge_content {
    h2 {
      margin-left: 1em;
      margin-bottom: .8em;
    }
    &_wrapper {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 14em;
      align-items: start;
      justify-content: space-between;
      padding-left: 11em;
      p {
        margin-bottom: 1.5em;
      }

      &_text_right {
        div {
          padding: 1.5em 2em;
          background: #776ACA;
          width: 100%;
          height: auto;
          font-weight: 400;
          color: #FFFFFF;
          position: relative;
          margin-bottom: 1.5em;
          span {
            font-weight: 600;
          }
          &:before {
            content: '';
            display: inline-block;
            position: absolute;
            top: 0;
            left: 0;
            border: .5em solid transparent;
            border-left: .5em solid #f5f8ff;
            border-top: .5em solid #f5f8ff;
          }
          &:after {
            content: '';
            display: inline-block;
            position: absolute;
            bottom: 0;
            right: 0;
            border: .5em solid transparent;
            border-right: .5em solid #f5f8ff;
            border-bottom: .5em solid #f5f8ff;
          }
        }
      }
      &_text_right, &_text_left {
        P:last-child {
          margin-bottom: 0;
        }
        p {
          font-weight: 400;
          color: #252C32;
          span, a {
            color: rgba(149, 136, 232, 1);
          }
        }
      }

    }
    &_img {
      margin-top: 9.6em;
      img {
        max-width: 1287px;
        width: 100%;
        will-change: transform;
      }
    }
  }
  &_solution_content {
    margin-top: 3.2em;
    margin-bottom: 7.2em;
    &_text {
      max-width: 40em;
      width: 100%;
      padding-left: 6.9em;
      p {
        font-weight: 400;
        color: #252C32;
        margin-bottom: 1.5em;
        span, a {
          color: rgba(149, 136, 232, 1);
        }
      }
      p:last-child {
        margin-bottom: 0;
      }
      div {
        padding: 0.688em 1.5em;
        gap: 1.6em;
        background: #776ACA;
        font-weight: 600;
        color: #FFFFFF;
        margin-bottom: 1em;
      }
    }
    &_wrapper {
      margin-top: 3.2em;
      display: grid;
      grid-template-columns: repeat(2, auto);
      align-items: start;
      justify-content: space-between;
      &_item {
        width: 100%;
        height: 100%;
        &_img {
          max-width: 630px;
          will-change: transform;
        }
        &_description {
          margin-top: .75em;
          font-weight: 600;
          text-align: center;
          color: #252C32;
        }
      }
    }
  }
  &_results_content {
    padding-left: 11em;
    &_wrapper {
      display: grid;
      grid-template-columns: repeat(2, auto);
      align-items: center;
      &_list {
        max-width: 55.5em;
        width: 100%;
        margin-top: 6em;
        position: relative;
        &:before {
          content: '';
          position: absolute;
          display: block;
          background: url(../img/result_oil_gas_vector.png) no-repeat;
          top: -6em;
          background-size: contain;
          left: -10.9em;
          width: 7.2em;
          z-index: 2;
          height: 100%;
        }
        &:after {
          content: '';
          position: absolute;
          display: block;
          height: 100%;
          width: .2em;
          background: #788188;
          top: 0;
          left: -3.9em;
          z-index: 2;
        }
        div {
          position: relative;
          font-weight: 400;
          color: #252C32;
          //height: 42px;
          margin-bottom: 2em;
          &:before {
            //content: '';
            //display: inline-block;
            //width: .666em;
            //height: 1.5em;
            //position: absolute;
            //top: 50%;
            //transform: translateY(-50%);
            //left: -2.6998em;
            //background: #252C32;
            //transition: .3s;
            //z-index: 3;
            content: '';
            display: inline-block;
            width: 0.63em;
            height: 1.5em;
            position: absolute;
            top: 0em;
            left: -2.68em;
            background: #252C32;
            transition: .3s;
            z-index: 22;
          }
        }
      }
      &_img {
        max-width: 63em;
        width: 100%;
        //padding-right: 5.6em;
        img {
          width: 100%;
          will-change: transform;
          // mix-blend-mode: multiply;
        }
      }
    }
  }
  &_encryption {
    width: 100%;
    &_title {
      display: flex;
      align-items: center;
      justify-content: center;
      h2 {
        font-family: 'Orbitron', sans-serif;
        font-weight: 600;
        //font-size: 2.8em;
        line-height: 140%;
        color: #252C32;
        margin-bottom: 1.44em;
        span {
          color: #9588e8;
        }
      }
    }
    &_content {
      display: grid;
      justify-content: space-between;
      grid-auto-columns: auto;
      grid-template-columns: repeat(auto-fit, minmax(15em, 1fr));
      align-items: start;
      &_item {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;
        &:last-child {
          position: relative;
          &:before {
            content: '';
            position: absolute;
            display: block;
            width: 12em;
            height: 1em;
            left: -6em;
            bottom: -1em;
            background: url(../img/figure1.png) no-repeat;
            scale: 2;
            background-size: contain;
          }
          &:after {
            content: 'Inference';
            position: absolute;
            display: block;
            width: 12em;
            height: 1em;
            .text_m();
            left: -6em;
            bottom: -2em;
          }
          .Oil_Gas_encryption_content_item_img:before {
            content: none;
          }
        }
        &_img {
          display: grid;
          align-items: start;
          justify-content: space-between;
          grid-template-columns: 1fr auto;
          width: 100%;
          div {
            position: relative;
            max-width: 79px;
            width: 100%;
            margin: 0 auto;
            img {
              will-change: transform;
            }
          }
          &:before {
            content: '';
            display: inline-block;
            background: url(../img/arrow_encript.png) no-repeat;
            background-size: contain;
            width: 3em;
            transform: rotate(270deg);
            height: 3em;
            order: 2;
          }
        }
        &_title {
          max-width: 9em;
          font-family: 'Inter Tight', sans-serif;
          font-weight: 400;
          color: #252C32;
          span {
            display: block;
            color: #9588e8;
          }
        }
      }
    }
  }
}

@media all and (max-width: 1380px) {


  .Oil_Gas_challenge_content_wrapper {
    padding-left: 0;
    &_text_left {
      h2 {
        margin-left: 0;
      }
    }
  }
  .Oil_Gas_customer_content {
    padding-left: 0;
    h2 {
      margin-left: 0;
    }
  }
  .Oil_Gas_content_info {
    margin-left: -.8em;
  }
  .Oil_Gas_content_info::after {
    left: calc(~"70px - 1%");
  }
  .Oil_Gas_content_info::before {
    left: .8em;
    width: 6.4em;
  }
  .Oil_Gas_content_info_wrapper_item {
    padding: 1.1222em 2em;
    &_title {
      .text_m();
    }
    &_text {
      .text_m();
    }
  }
}
@media all and (max-width: 1100px) {
  .Oil_Gas_encryption_content {
    grid-template-columns: repeat(auto-fit, minmax(20em, 1fr));
  }
  .Oil_Gas_results_content_wrapper {
    grid-template-columns: auto;
    justify-content: normal;
    &_list {
      margin-top: 3.4em;
      &:before {
        top: -3em;
        left: -10.9em;
      }
    }
  }
  .Oil_Gas_content {
    grid-template-columns: auto;
    justify-content: normal;
    gap: 0;
    &_img {
      margin-bottom: 8.9em;
      margin-top: 4.2em;
      margin-left: 50%;
      transform: translateX(-50%);
    }
    &_info {
      order: 2;
      &:before, &:after {
        content: none;
      }
      &_wrapper {
        padding-left: 0;
        &_item {
          &:first-child {
            padding-top: 0;
          }
          &_title {
            .text_ml();
          }
          &_text {
            .text_m();
          }
        }
      }
    }
  }
}
@media all and (max-width: 980px) {
  .Oil_Gas_results_content_wrapper_list div:before {
    left: -2.45em;
  }
  .Oil_Gas_encryption_content {
    grid-template-columns: repeat(auto-fit, minmax(15em, 1fr));
  }
  .Oil_Gas_results_content_wrapper_list:before {
    top: -3.3em;
    left: -10.9em;
  }
  .Oil_Gas_solution_content_text {
    padding-left: 0;
    max-width: none;
  }
  .Oil_Gas_solution_content {
    margin: 4.5em 0;
  }
  .Oil_Gas_solution_content_wrapper {
    margin-top: 2.3em;
    grid-template-columns: auto;
    gap: 2.4em;
  }
  .Oil_Gas_challenge_content_wrapper {
    grid-template-columns: auto;
    gap: 0;
    &_text_right {
      margin-top: 2em;
    }
  }
  .Oil_Gas_customer_content {
    grid-template-columns: auto;
    gap: 0;
    &_text {
      h2 {
        margin-bottom: .371em;
      }
    }
    &_list {
      margin-top: 3.2em;
    }
  }
  .Oil_Gas_subtitle {
    margin-left: 0;
  }
}
@media all and (max-width: 700px) {
  .Oil_Gas {
    .text_l {
      font-size: 3.6em;
    }
  }
  .Oil_Gas_separator {
    margin-bottom: 0;
  }
  .Oil_Gas_content_img {
    margin-top: 4.4em;
    margin-bottom: 3.3em;
  }
  .Oil_Gas_encryption_content_item:last-child:before {
    width: 9em;
    height: 1em;
    left: auto;
    right: -1em;
    bottom: auto;
    top: -3.5em;
    scale: 2;
    transform: rotate(270deg);
  }
  .Oil_Gas_encryption_content_item:last-child:after {
    width: 12em;
    height: 1em;
    font-size: 1.6em;
    left: auto;
    right: -5em;
    top: -50%;
    transform: translateY(-50%) rotate(-90deg);
    bottom: auto;
  }
  .Oil_Gas_encryption_content {
    display: block;
    &_item {
      align-items: start;
      display: grid;
      grid-template-columns: repeat(2, auto);
      justify-content: start;
      &_img {
        align-items: center;
        justify-content: start;
        grid-template-columns: auto;
        grid-template-rows: auto auto;
        width: auto;
        &:before {
          transform: rotate(0);
          margin: 1.5em 0 1.5em 6em;
          width: 100%;
          height: 4em;
        }
      }
      &_title {
        max-width: 13em;
        margin-left: 1.4em;
        text-align: left;
        span {
          display: inline;
        }
      }
    }
  }
  .Oil_Gas section {
    overflow: hidden;
  }
  .Oil_Gas_challenge_content_wrapper_text_right {
    margin-top: 0;
  }
  .Oil_Gas_results_content_wrapper_img {
    max-width: 39.1em;
    width: auto;
    margin-left: -5%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 3.24em;
  }
  .Oil_Gas_results_content {
    padding-left: 6em;
  }
  .Oil_Gas_results_content_wrapper_list:before {
    top: -1.5em;
    left: -5.3em;
    background: url(../img/lineoil.png) no-repeat !important;
  }
  .Oil_Gas_results_content_wrapper_list:after {
    width: 2px;
  }
  .Oil_Gas_content_info_wrapper_item {
    min-height: 6em;
    justify-content: space-between;
    &_text {
      text-align: right;
    }
  }
  .Oil_Gas_solution_content {
    margin-top: 0;
    margin-bottom: 0;
  }
  .Oil_Gas_solution_content {
    margin-top: 1.1em;
  }
  .Oil_Gas h2  {
    margin-bottom: .58em;
  }
  .Oil_Gas_challenge_content h2 {
    margin-left: 0;
  }
  .Oil_Gas_customer_content_list {
    margin-top: 3.3em;
  }
  .Oil_Gas_customer_content_list_title {
    margin-bottom: .82em;
    max-width: 15em;
  }
  .Oil_Gas_customer_content_list_item {
    margin-bottom: .51em;
  }
  .Oil_Gas_customer_content_text p, .Oil_Gas_challenge_content_wrapper_text_right p, .Oil_Gas_challenge_content_wrapper_text_left p,
  .Oil_Gas_solution_content_text p, .Oil_Gas_challenge_content_wrapper_text_right div, .Oil_Gas_solution_content_text div {
    margin-bottom: 1.1em;
  }
  .Oil_Gas_solution_content_wrapper {
    gap: 0;
  }
  .Oil_Gas_solution_content_wrapper_item_img {
    margin-bottom: 3.24em;
    margin-top: 1.1em;
  }
  .Oil_Gas_solution_content_wrapper_item_description {
    margin-top: 0;
    margin-bottom: 1.2em;
  }
  .result_title {
    margin-bottom: .9199em;
  }
  .Oil_Gas_results_content_wrapper_list div {
    margin-bottom: 1.52em;
  }
  .Oil_Gas {
    margin-bottom: 3.2em;
  }
}
@media all and (max-width: 480px) {
  .Oil_Gas_results_content_wrapper_list div:before {
    left: -2.12em;
  }
}

@media all and (max-width: 444px) {
  .Oil_Gas_results_content_wrapper_list::before {
    left: -5.4em;
  }
}
@media all and (max-width: 375px) {
  .Oil_Gas_results_content_wrapper_list::before {
    left: -5.7em;
    width: 3em;
  }
}
@media all and (max-width: 320px) {
  .Oil_Gas_results_content_wrapper_list::before {
    left: -6em;
    width: 3em;
  }
}
