//     : @import './src/less/blocks/technology.less';



.technology {
    padding: 18.9em 0 22em 0;
    
    @media all and (max-width: 980px) {
        padding-top: 10em;
        padding-bottom: 3.5em;
    }
    @media all and (max-width: 700px) {
        padding-top: 1em;
        padding-bottom: 8em;
    }
    @media all and (max-width: 480px) {
        padding-top: 4.6em;
    }
    
    .container {
        
        .line_separator {
            margin-top: 4.7em;
            width: 63em;
            position: relative;
            z-index: 10;
            
            @media all and (max-width: 980px) {
                width: 100%;
                margin-top: 5.3em;
            }
        }
        
        .knight_wrapper {
            position: relative;
            
            
            .knight_list {
                display: grid;
                grid-template-columns: 1fr;
                grid-gap: 5.4em;
                width: 52em;
                padding-top: 5.6em;
                position: relative;
                z-index: 10;
                
                @media all and (max-width: 1100px) {
                    width: 42em;
                }
                @media all and (max-width: 980px) {
                    width: 63em;
                    padding-top: 6.3em;
                    grid-gap: 6em;
                }
                @media all and (max-width: 700px) {
                    width: 100%;
                    grid-gap: 4.2em;
                }
                @media all and (max-width: 480px) {
                    padding-top: 5.3em;
                }
                
                &:before {
                    content: '';
                    display: inline-block;
                    width: 8em;
                    height: 44.3em;
                    position: absolute;
                    top: 0.5em;
                    left: 0;
                    background-image: url(../img/tech_line.svg);
                    background-size: contain;
                    background-repeat: no-repeat;
                    background-position: center;
                    
                    @media all and (max-width: 980px) {
                        left: 0.8em;
                    }
                    @media all and (max-width: 700px) {
                        left: auto;
                        right: ~'calc(100% - 3.1em)';
                    }
                }
                
                .knight_point {
                    padding-left: 3em;
                    margin-left: 8em;
                    position: relative;
                    cursor: pointer;
                    
                    @media all and (max-width: 980px) {
                        margin-left: 8.7em;
                        padding-left: 3.3em;
                    }
                    @media all and (max-width: 700px) {
                        margin-left: 3em;
                        padding-left: 3em;
                    }
                    
                    &:after {
                        content: '';
                        display: inline-block;
                        position: absolute;
                        top: 50%;
                        transform: translateY(-50%);
                        left: 1.8em;
                        background-color: @violet;
                        height: ~'calc(100% + 2em)';
                        width: 100%;
                        z-index: 1;
                        transition: .3s;
                        opacity: 0;
                        background-image: url('../img/main/point_bgr.png');
                        //background-size: 20em;
                        background-size: contain;
                        background-position: 100% 50%;
                        background-repeat: no-repeat;
                        
                        @media all and (max-width: 480px) {
                            width: ~'calc(100% - 1.8em)';
                        }
                    }
                    
                    &:before {
                        content: '';
                        display: inline-block;
                        width: 1em;
                        height: 2.4em;
                        position: absolute;
                        top: 0.1em;
                        left: -0.5em;
                        background: @dark;
                        transition: .3s;
                    }
                    
                    p {
                        position: relative;
                        z-index: 10;
                        transition: .3s;
                        
                        
                    }
                }
                .knight_point.active {
                    &:after {
                        opacity: 1;
                    }
                    &:before {
                        background: @white;
                        box-shadow: 0px 0px 14px 0px #9588E8;
                        box-shadow: 0px 4px 14px 0px #C9C2F9;
                    }
                    p {
                        color: @white;
                    }
                }
            }
            
            .knight_item {
                position: absolute;
                width: 124.7em;
                height: 112.3em;
                top: -43.8em;
                left: 35.8em;
                
                @media all and (max-width: 1100px) {
                    left: 22em;
                }
                @media all and (max-width: 980px) {
                    font-size: 0.8em;
                    position: relative;
                    top: -19em;
                    left: -4em;
                    margin-bottom: -15em;
                }
                @media all and (max-width: 700px) {
                    font-size: 0.7em;
                    left: -17em;
                }
                @media all and (max-width: 480px) {
                    font-size: 0.65em;
                    left: -18.8em;
                    top: -9.5em;
                }
                
                #knight_item_skel {
                    position: relative;
                    left: -2.5em;
                    top: 7.4em;
                    canvas {
                        position: relative;
                        z-index: -1;
                    }
                }
                
                .circle {
                    position: absolute;
                    transform: translate(-50%, -50%);
                    top: 50em;
                    left: 38em;
                    width: 71em;
                    height: 71em;
                    transform-origin: 50% 50%;
                }
                
                .circle_1 {
                    animation: rotate 60s linear infinite;
                }
                .circle_2 {
                    animation: rotate 60s linear infinite reverse;
                }
                
                @keyframes rotate {
                    0% { transform: translate(-50%, -50%) rotate(0deg); }
                    0% { transform: translate(-50%, -50%) rotate(360deg); }
                }
                
                .knight_item_number {
                    position: absolute;
                    border-radius: 50%;
                    transform: translate(-50%, -50%);
                    transform-origin: 50% 50%;
                    width: 5.9em;
                    height: 5.9em;
                }
                .inner {
                    background: rgba(255,255,255,.25);
                    box-shadow: 0px 21.59px 30.85px 0px #00000033;
                    width: 5.9em;
                    height: 5.9em;
                    transition: .3s;
                    position: absolute;
                    border-radius: 50%;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    cursor: pointer;
                    
                    @media all and (max-width: 700px) {
                        // font-size: 1.5em;
                        width: 8em;
                        height: 8em;
                    }
                }
                
                .number_item {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-family: 'Orbitron';
                    transform: translate(-50%, -50%);
                    border-radius: 50%;
                    background: @white;
                    font-size: 1.8em;
                    width: 2.25em;
                    height: 2.25em;
                    transition: .3s;
                    @media all and (max-width: 700px) {
                        font-size: 2.6em;
                    }
                }
                .line_item {
                    position: absolute;
                    opacity: 0;
                    transition: .3s;
                    background-size: contain;
                    background-repeat: no-repeat;
                    background-position: center;
                    visibility: hidden;
                }
                .knight_item_number.active {
                    
                }
                .inner.active {
                    box-shadow: 0px 33.3px 47.58px 0px #00000033;
                    transform: translate(-50%, -50%) scale(1.5);
                }                
                .line_item.active {
                    opacity: 1;
                    visibility: visible;
                }
                .inner.active .number_item {
                    background: @violet;
                    color: @white;
                }
                
                .num_wrapper_1 {
                    top: 27.9em;
                    left: 76.7em;
                    z-index: 5;
                    
                    @media all and (max-width: 980px) { left: 79.7em; }
                    
                }
                .line_item_1 {
                    background-image: url('../img/main/line_1.svg');
                    width: 72.8em;
                    height: 29.2em;
                    bottom: 63.6em;
                    right: 51.1em;
                    
                    @media all and (max-width: 980px) {
                        background-image: url('../img/main/line_700_1.svg');
                        width: 20.6em;
                        height: 56.5em;
                        bottom: 85.6em;
                        right: 30.7em;
                    }
                    @media all and (max-width: 700px) {
                        height: 52.5em;
                    }
                    @media all and (max-width: 480px) {
                        background-image: url('../img/main/line_378_1.svg');
                        right: 29.5em;
                        height: 64em;
                        bottom: 88.6em;
                    }
                }
                .num_wrapper_2 {
                    top: 57em;
                    left: 71.7em;
                    z-index: 4;
                    
                    @media all and (max-width: 480px) {
                        top: 55em;
                        left: 74.7em;
                    }
                    
                }
                .line_item_2 {
                    background-image: url('../img/main/line_2.svg');
                    width: 59.7em;
                    height: 7.6em;
                    bottom: 44.3em;
                    right: 55.2em;
                    
                    @media all and (max-width: 1100px) {
                        width: 66.7em;
                        height: 9.6em;
                        bottom: 45.3em;
                        right: 53.2em;
                    }
                    
                    @media all and (max-width: 980px) {
                        background-image: url('../img/main/line_700_2.svg');
                        bottom: 57.9em;
                        right: 36.6em;
                        width: 20.8em;
                        height: 71.5em;
                    }
                    @media all and (max-width: 480px) {
                        background-image: url('../img/main/line_378_2.svg');
                        bottom: 60.9em;
                        right: 29.6em;
                        width: 23.8em;
                        height: 80.5em;
                    }
                }
                .num_wrapper_3 {
                    top: 45.9em;
                    left: 45.7em;
                    z-index: 3;
                    @media all and (max-width: 980px) { left: 51.7em; }
                    @media all and (max-width: 480px) { left: 52.7em; }
                }
                .line_item_3 {
                    background-image: url('../img/main/line_3.svg');
                    width: 34.5em;
                    height: 19.5em;
                    bottom: 47.1em;
                    right: 83.1em;
                    
                    @media all and (max-width: 980px) {
                        background-image: url('../img/main/line_700_3.svg');
                        width: 34.3em;
                        height: 54.3em;
                        bottom: 66.1em;
                        right: 37.1em;
                    }
                    @media all and (max-width: 480px) {
                        background-image: url('../img/main/line_378_3.svg');
                        width: 39.3em;
                        height: 64.3em;
                        bottom: 65.6em;
                        right: 33.9em;
                    }
                }
                .num_wrapper_4 {
                    top: 63em;
                    left: 26.6em;
                    z-index: 2;
                    
                    @media all and (max-width: 980px) {
                        top: 57.8em;
                        left: 39.6em;
                    }
                    @media all and (max-width: 480px) {
                        top: 51.8em;
                    }
                }
                .line_item_4 {
                    background-image: url('../img/main/line_4.svg');
                    width: 15.0em;
                    height: 7.5em;
                    bottom: 39.7em;
                    right: 101.6em;
                    
                    @media all and (max-width: 1100px) {
                        width: 17em;
                        height: 9.5em;
                        bottom: 38.7em;
                        right: 99.6em;
                    }
                    
                    @media all and (max-width: 980px) {
                        background-image: url('../img/main/line_700_4.svg');
                        width: 48.8em;
                        height: 53.3em;
                        bottom: 54.5em;
                        right: 37.7em;
                    }
                    @media all and (max-width: 480px) {
                        background-image: url('../img/main/line_378_4.svg');
                        width: 57.8em;
                        height: 55.3em;
                        bottom: 60.1em;
                        right: 34.4em;
                    }
                }
                .num_wrapper_5 {
                    top: 57.7em;
                    left: 43.4em;
                    z-index: 1;
                    @media all and (max-width: 980px) {
                        top: 63.7em;
                        left: 51.4em;
                    }
                    @media all and (max-width: 480px) {
                        top: 59.7em;
                        left: 48.4em;
                    }
                }
                .line_item_5 {
                    background-image: url('../img/main/line_5.svg');
                    width: 34.6em;
                    height: 24.9em;
                    bottom: 29.6em;
                    right: 85.4em;
                    
                    @media all and (max-width: 980px) {
                        background-image: url('../img/main/line_700_5.svg');
                        width: 42.1em;
                        height: 47.4em;
                        bottom: 48em;
                        right: 34.1em;
                    }
                    
                    @media all and (max-width: 480px) {
                        background-image: url('../img/main/line_378_5.svg');
                        width: 51.1em;
                        height: 53.4em;
                        bottom: 53em;
                        right: 39.1em;
                    }
                }
            }
        }
        
    }
}
